const replaceSpecialCharacters = (passage, vocab, translation) => {
    // console.log(passage);
    let _passage = passage;
    const transfer = [
        {
            from: "&*",
            to: "+ + + + +",
        },
        {
            from: `<span <span="" class="sr-examples-word sr-word-similar sr-cl-secondary"> style"fontweight </span>`,
            to: "",
        },
        {
            from: `<span <span="" class="sr-examples-word sr-word-similar sr-cl-secondary"> style"backgroundcolor </span>`,
            to: "",
        },
        {
            from: `var(--bs-body-font-weight);`,
            to: "",
        },
        {
            from: `<span class="sr-examples-word sr-word-opposite sr-cl-danger"> textalign </span> var(--bs-body-text-align);"&gt;`,
            to: "",
        },
        {
            from: `color <span class="sr-examples-typeWord"> (n) </span> <span class="sr-examples-spelling sr-cl-yellow"> /'kələr/ </span> red; cursor <span class="sr-examples-typeWord"> (n) </span> <span class="sr-examples-spelling sr-cl-yellow"> /'kɜrsər/ </span> pointer; transform <span class="sr-examples-typeWord"> (n) </span> <span class="sr-examples-spelling sr-cl-yellow"> /træns'foʊrm/ </span> translate(-3px, <span class="sr-examples-word sr-word-opposite sr-cl-danger"> 5px); </span> "&gt;x`,
            to: '"',
        },
        {
            from: `<span class="im-inliner-orig-text"> `,
            to: "",
        },
        {
            from: ` </span> <inliner class="im-inliner-dst-text" <span="">style"color   #109e36; <span class="sr-examples-word sr-word-opposite sr-cl-danger">paddingright</span>   8px;"&gt;`,
            to: "_",
        },
        {
            from: `</span> <inliner class="im-inliner-dst-text" <span class="sr-examples-word sr-word-similar sr-cl-secondary">style"color</span>   #109e36; <span class="sr-examples-word sr-word-opposite sr-cl-danger">paddingright</span>   8px;">`,
            to: "_",
        },
        {
            from: `<inliner style="`,
            to: "",
        },
        {
            from: `</span><inliner class="im-inliner-dst-text" style="color: #109e36; padding-right: 8px;">`,
            to: "",
        },
        {
            from: `color <span class=" sr-examples-typeword"="">(n) <span class="sr-examples-spelling sr-cl-yellow">/'kələr/</span> red;
            cursor <span class="sr-examples-typeWord">(n)</span> <span class="sr-examples-spelling sr-cl-yellow">/'kɜrsər/</span> pointer;`,
            to: "",
        },
        {
            from: `transform <span class="sr-examples-typeWord">(n)</span> <span class="sr-examples-spelling sr-cl-yellow">/træns'foʊrm/</span> translate(-3px, <span class="sr-examples-word sr-word-opposite sr-cl-danger">5px);</span>  "&gt; x </inliner> </inliner>`,
            to: "",
        },
        {
            from: "&nbsp;",
            to: " ",
        },
        {
            from: "<br>===New Info===<br>",
            to: "",
        },
        {
            from: "$$$",
            to: `+${vocab}ed: ${translation} <br/>+${vocab}ing: ${translation}`,
        },
        {
            from: "$$",
            to: translation,
        },
        {
            from: "$",
            to: vocab,
        },
    ];
    // console.log({ _passage });
    // _passage = _passage.replaceAll(`\"`, `"`);
    transfer.forEach(({ from, to }) => {
        _passage = _passage.replaceAll(from, to);
        // console.log({ include: _passage.includes(from), from, to });
    });

    // console.log({ _passage });

    return _passage;
};

export default replaceSpecialCharacters;
