const chatGPTStyle = `*:not(:empty) {
    background: #353541;
    background: #8899ab;
    background: #16202a;
    border: none;
}

*[class*="gradient"] {
    background: none !important;
}

*[class*="dark:bg-gray-800"] {
    background: transparent !important;
}

h1 {
    display: none !important;
}



textarea#prompt-textarea {
    background: none;
    border: none;
}

form * {
    /* background: none !important;
    border: none !important;
    box-shadow: none !important; */
    /* border-bottom: 1px solid black !important; */
}

form + div {
    /* display: none; */
    opacity: 0;
    /* padding: 10px !important; */
    /* height: 20px; */
}

form + div > span {
    display: none;
}

form div:not([class*="dark"]), form {
    background: transparent !important;
}

div[class*="flex-shrink-0"][style*="width: 260px"] {
    width: 30px !important;
    height: 30px;
    border-radius: 999px;
    overflow: hidden !important;
    border: 1px solid #ccc;
    /* background: red !important; */
    transition: all 0.5s ease-out;
}

div[class*="flex-shrink-0"][style*="width: 260px"]:hover {
    width: 260px !important;
    height: 100%;
    overflow: auto !important;
    border-radius: 10px;
}

img[alt*="User"],
div[style*="background-color: rgb(25, 195, 125);"],
div[class*="flex justify-between empty:hidden lg:block"],
form[class*="stretch"],
header,
div[class*="sticky"]:is([class*="md:hidden"]) {
    opacity: 0;
    cursor: pointer;
    transition: all 0.3s ease;
}

img[alt*="User"]:hover,
div[style*="background-color: rgb(25, 195, 125);"]:hover,
div[class*="flex justify-between empty:hidden lg:block"]:hover,
form[class*="stretch"]:hover,
form[class*="stretch"]:has(textarea:focus),
header:hover,
div[class*="sticky"]:is([class*="md:hidden"]):hover {
    opacity: 1;
    /* transition: all .3s ease; */
}

div[class*="flex flex-1 gap-4 text-base mx-auto md:gap-6 md:max-w-2xl lg:max-w-[38rem] }"] {
    max-width: 60rem;
    margin-right: auto !important;
}

@media (min-width: 800px) {
    div[class*="flex flex-1 gap-4 text-base mx-auto md:gap-6 md:max-w-2xl lg:max-w-[38rem] }"] {
        margin-left: 100px;
    }

    [class*="p-4 justify-center text-base"] > [class*="flex flex-1 gap-4 text-base mx-auto"] {
        margin-left: 88px;
        max-width: 897px;
    }
}

div[class*="dark:bg-gray-800"][style*="--avatar-color: #19c37d;"] * {
    color: #8957e5 !important;
    /* margin-left: 10px; */
    /* text-align: center; */
}

div[class*="dark:bg-gray-800"][style*="--avatar-color: #19c37d;"]
    > div[class*="p-4"] {
    padding: 5px;
}

/* div[class*=gradient] {
    background: transparent !important;
} */

/* div[class*="h-32 md:h-48 flex-shrink-0"] {
    display: none;
} */

strong {
    color: #cec4ac !important;
    color: #8957e5 !important;
}
`;

export default chatGPTStyle;
