import React, { useState } from "react";
import PropTypes from "prop-types";
import convertMilliseconds from "../utils/convertMilliseconds";
import schedule from "../utils/schedule";
import API_VOCA from "../firebase/API_VOCA";
import { isNotTestMode } from "../utils/global";
import textToHighlight from "../utils/textToHighlight";
import { useEffect } from "react";
import { useRef } from "react";
import findSimilarWords from "../utils/findSimilarWords";
import localStorage, { MISSED_WORDS_KEY } from "../utils/localStorage";
import InputCreateSentence from "./InputCreateSentence";
import { getDatamuse, wordsApi } from "../dictionaryApi/dictionaryapi";
import Spinner from "./Spinner";
import replaceSpecialCharacters from "../utils/replaceSpecialCharacters";
import Gallery from "./Gallery";
import COLLECTIONS from "../firebase/collections";

// const missedWordsLocalStorage = localStorage(MISSED_WORDS_KEY);
// const isNotTestMode = false;

const CardItem = (props) => {
    const [mySentences, setMySentences] = useState({
        isShowInput: false,
    });
    const [galleryAlbums, setGalleryAlbums] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [wordInfo, setWordInfo] = useState(props);
    const [isShowImage, setIsShowImage] = useState(false);
    const {
        voca,
        translate,
        meaning,
        imageInfo,
        examples,
        note,
        spacedRepetition,
        time,
        spelling,
        similar,
        typeWords,
        repetitiveTime,
        level,
        id,
        indexInData,
        setData,
        setMissedWords,
        setUpperDataSearch,
        indexInUpperData,
        searchText,
        data,
        simple = false,
        addEvent = false,
    } = props;

    const [showDetail, setShowDetail] = useState(searchText ? true : false);
    const [similes, setSimiles] = useState(similar);

    const [moreRelated, setMoreRelated] = useState({
        similarMeaningWords: [],
        similarSoundWords: [],
    });

    const cardItemRef = useRef(null);
    const noteRef = useRef(null);

    const currentTime = new Date().getTime();
    const remainingTime = repetitiveTime - currentTime;
    const isNewWord = time?.length <= 2 ? true : false;

    useEffect(() => {
        // 12h = 43200000
        // console.log(remainingTime);
        if (props.nextLevel) {
            // console.log("show voca: ", voca);
            if (remainingTime <= 0) {
                // console.log("add", { level, voca });
                const time = Math.random() * 5;
                // console.log(time);
                // const timeDelay = 43200000 * 6; // 3 days
                setTimeout(async () => {
                    // const _data = [...data];
                    // const _updateData = {
                    //     ..._data[indexInData],
                    // };
                    await handleMissedWords();
                    await handleNextLevel();

                    // console.log({ voca, level, nextLevel: level + 1 });
                }, time);
            }
        }
    }, [props.nextLevel]);

    useEffect(() => {
        // console.log(indexInData, searchText, !props.nextLevel);
        if ((indexInData === 1 || searchText) && !props.nextLevel) {
            async function getRelatedWords() {
                // console.log()
                const [spellLike, meaninglike] = await Promise.all([
                    getDatamuse(voca, "sp"),
                    getDatamuse(voca, "ml"),
                ]);
                setMoreRelated({
                    similarMeaningWords: meaninglike?.slice(0, 10),
                    similarSoundWords: spellLike?.slice(1, 10),
                });
                // console.log({
                //     similarMeaningWords: spellLike,
                //     similarSoundWords: meaninglike,
                // });
            }

            getRelatedWords();
        }

        const handleKeyDown = (e) => {
            // e.preventDefault();
            // e.stopPropagation();
            // console.log("some thing");
            // console.log(e.key);

            if (e.key === "ArrowLeft") {
                // console.log({ voca });
                handlePrevLevel();
            } else if (e.key === "ArrowRight") {
                handleNextLevel();
            } else if (e.key === "ArrowUp" || e.key === "ArrowDown") {
                e.preventDefault();
                setShowDetail((prev) => !prev);
            }
        };

        // add event
        if (indexInData === 0 && !searchText && addEvent) {
            // console.log("add event: ", voca);
            // cardItemRef.current?.getElementById("note")?.focus();
            // noteRef.current?.focus();
            window.addEventListener("keydown", handleKeyDown);
        }
        return () => {
            window.removeEventListener("keydown", handleKeyDown);
        };
    }, [indexInData, addEvent]);

    // too much vocabulary remain
    // useEffect(() => {
    //     // 12h = 43200000
    //     // console.log(remainingTime);
    //     if (remainingTime < 43200000) {
    //         const time = Math.random() * 5;
    //         console.log(time);
    //         const timeDelay = 43200000 * 6; // 3 days
    //         setTimeout(async () => {
    //             // handlePrevLevel();
    //             const updateData = {
    //                 repetitiveTime: repetitiveTime + timeDelay, // end of time = _time[_time.length - 1]
    //             };

    //             console.log(
    //                 convertMilliseconds(timeDelay).toString({
    //                     seconds: false,
    //                 })
    //             );

    //             // Update API
    //             if (isNotTestMode) {
    //                 await API_VOCA.updateWord(id, updateData);
    //             }
    //             // console.log("prev level");
    //         }, time);
    //     }
    // }, []);

    const handleNextLevel = async () => {
        const currentTime = new Date().getTime();
        let _time = [];
        let nextLevel = level + 1;

        // console.log({ level, voca, indexInData });
        if (level + 1 >= schedule.normal.length) {
            console.log("===== RETURN LEVEL 7 => 5 =====");
            _time = [currentTime, currentTime + schedule.normal[5]];
            nextLevel = 5;
        } else {
            _time = [...time, currentTime + schedule.normal[level + 1]];
        }

        const updateData = {
            time: _time,
            level: nextLevel,
            repetitiveTime: _time[_time.length - 1], // end of time = _time[_time.length - 1]
        };

        // Not important
        // const _remainingTime = updateData.repetitiveTime - currentTime;
        // console.log(
        //     convertMilliseconds(_remainingTime).toString({
        //         seconds: true,
        //     })
        // );

        // Update API
        if (isNotTestMode) {
            await API_VOCA.updateWord(id, updateData);
        }
        // Update Ui
        setData((prevData) => {
            const _actualIndex = props.nextLevel
                ? prevData.findIndex((word) => word.id === id)
                : indexInData;
            const newData = [
                ...prevData,
                {
                    ...prevData[_actualIndex],
                    ...updateData,
                    note: wordInfo.note,
                    examples: wordInfo.examples,
                },
            ];
            // console.log({
            //     voca,
            //     newVoca: prevData[indexInData].voca,
            //     Data: prevData[indexInData],
            //     indexInData,
            //     _actualIndex,
            //     dataExists: prevData[_actualIndex],
            // });
            // console.log({ indexInData });
            newData.splice(_actualIndex, 1);
            return newData;
        });
    };

    const handlePrevLevel = async () => {
        const currentTime = new Date().getTime();
        let _time = time;
        const timeLength = time.length;
        // const prevLevel = level - 1 <= 0 ? 0 : level - 1;
        const prevLevel = 1;

        if (true) {
            // console.log(_time);
            // _time = [..._time, currentTime + schedule.normal[prevLevel - 2]];

            const updateData = {
                level: prevLevel,
                repetitiveTime: currentTime + schedule.normal[prevLevel], // end of time = _time[_time.length - 1]
            };

            // Not important
            // const _remainingTime = updateData.repetitiveTime - currentTime;
            // console.log(
            //     convertMilliseconds(_remainingTime).toString({
            //         seconds: true,
            //     }),
            //     prevLevel
            // );

            // Update API
            if (isNotTestMode) {
                await API_VOCA.updateWord(id, updateData);
            }

            // Update Ui
            setData((prevData) => {
                const _updateData = {
                    ...prevData[indexInData],
                    ...updateData,
                    note: wordInfo.note,
                    examples: wordInfo.examples,
                };
                const newData = [...prevData, _updateData];
                handleMissedWords(_updateData);
                newData.splice(indexInData, 1);
                return newData;
            });
        }
    };

    const handleKeepLevel = async () => {
        const _updateData = {
            ...data[indexInData],
        };
        await handleMissedWords(_updateData);
        const currentTime = new Date().getTime();

        const timeDelay = schedule.normal[0];

        if (true) {
            const updateData = {
                repetitiveTime: currentTime + timeDelay, // end of time = _time[_time.length - 1]
            };

            // Update API
            if (isNotTestMode) {
                await API_VOCA.updateWord(id, updateData);
            }

            // Update Ui
            setData((prevData) => {
                const _updateData = {
                    ...prevData[indexInData],
                    ...updateData,
                };
                const newData = [...prevData, _updateData];
                handleMissedWords(_updateData);
                newData.splice(indexInData, 1);
                return newData;
            });
        }
    };

    // const handlePrevLevel = async () => {
    //     const currentTime = new Date().getTime();
    //     let _time = time;
    //     const timeLength = time.length;

    //     if (timeLength >= 2) {
    //         if (timeLength == 2) {
    //             _time.pop();
    //         } else {
    //             _time.pop();
    //             _time.pop();
    //         }
    //         // console.log(_time);
    //         _time = [..._time, currentTime + schedule.normal[timeLength - 2]];

    //         const updateData = {
    //             time: _time,
    //             level: timeLength - 2,
    //             repetitiveTime: _time[_time.length - 1], // end of time = _time[_time.length - 1]
    //         };

    //         // Not important
    //         const _remainingTime = updateData.repetitiveTime - currentTime;
    //         console.log(
    //             convertMilliseconds(_remainingTime).toString({
    //                 seconds: true,
    //             })
    //         );

    //         // Update API
    //         if (isNotTestMode) {
    //             await API_VOCA.updateWord(id, updateData);
    //         }

    //         // Update Ui
    //         setData((prevData) => {
    //             const newData = [
    //                 ...prevData,
    //                 {
    //                     ...prevData[indexInData],
    //                     ...updateData,
    //                     note: wordInfo.note,
    //                     examples: wordInfo.examples,
    //                 },
    //             ];

    //             if (setMissedWords) {
    //                 setMissedWords((missedWordsPrevData) => {
    //                     const newData = [
    //                         ...missedWordsPrevData,
    //                         {
    //                             ...prevData[indexInData],
    //                             ...updateData,
    //                             note: wordInfo.note,
    //                             examples: wordInfo.examples,
    //                         },
    //                     ];
    //                     return newData;
    //                 });
    //             }
    //             newData.splice(indexInData, 1);
    //             return newData;
    //         });
    //     }
    // };

    const handleDeleteWord = async () => {
        // eslint-disable-next-line no-restricted-globals
        if (
            // eslint-disable-next-line no-restricted-globals
            confirm("Are you sure you want to remove it out of the database?")
        ) {
            // Save it!
            if (isNotTestMode) {
                await API_VOCA.deleteWord(id);
            }

            // Update Ui
            setData((prevData) => {
                const newData = [...prevData];
                newData.splice(indexInData, 1);
                return newData;
            });
            // console.log('Thing was saved to the database.');
        } else {
            // Do nothing!
            console.log("Thing was not saved to the database.");
        }
        // Update API
    };

    const handleOnBlur = async (e) => {
        // console.log(e.target.innerHTML);
        e.preventDefault();
        e.stopPropagation();
        const target = e.target;
        if (props[target.id].trim() !== target.innerText.trim()) {
            const updateData = {
                [target.id]: target.innerText,
            };
            if (isNotTestMode) {
                await API_VOCA.updateWord(id, updateData);
            }

            // handle UI
            // console.log({ setUpperDataSearch });
            if (setUpperDataSearch) {
                setUpperDataSearch((prevData) => {
                    // console.log(prevData[indexInUpperData]);
                    prevData[indexInUpperData][target.id] = target.innerText;
                    // console.log(prevData[indexInUpperData]);
                    return prevData;
                });
            }
        }
        // console.log({ updateData });
    };

    const handleKeydownHighlight = (e) => {
        e.stopPropagation();
        if (e.keyCode == "50") {
            handleOnBlurHighlight(e, false);
            // var range = document.createRange(); // Tạo một đối tượng Range
            // range.selectNodeContents(e.target); // Chọn toàn bộ nội dung của thẻ div
            // range.collapse(false); // Di chuyển con trỏ chuột đến cuối của văn bản

            // var selection = window.getSelection(); // Lấy đối tượng Selection
            // selection.removeAllRanges(); // Xóa bỏ tất cả các range hiện tại
            // selection.addRange(range); // Thêm range mới đã được thiết lập
            // console.log({ range, selection });
        }
    };
    const handleOnBlurHighlight = async (e, updateApi = true) => {
        e.stopPropagation();
        // console.log(e.target.innerHTML);
        const target = e.target;
        const _plainText = target.innerHTML;

        const plainText = replaceSpecialCharacters(
            _plainText,
            wordInfo.voca,
            wordInfo.translate
        );

        if (wordInfo[target.id].trim() !== plainText.trim()) {
            const highlightText = await textToHighlight(plainText);
            const updateData = {
                [target.id]: replaceSpecialCharacters(
                    highlightText,
                    wordInfo.voca,
                    wordInfo.translate
                ),
            };
            // console.log({ updateData });

            if (updateApi && isNotTestMode) {
                await API_VOCA.updateWord(id, updateData);
            }
            // target.innerHTML = highlightText;
            setWordInfo((prev) => ({
                ...prev,
                [target.id]: replaceSpecialCharacters(
                    highlightText,
                    wordInfo.voca,
                    wordInfo.translate
                ),
            }));
            // console.log("on Blur");

            // Update Ui
            setData((prevData) => {
                prevData[indexInData] = {
                    ...prevData[indexInData],
                    ...updateData,
                };
                return prevData;
            });

            // handle UI
            // console.log({ setUpperDataSearch, indexInUpperData });
            if (setUpperDataSearch && indexInUpperData >= 0) {
                setUpperDataSearch((prevData) => {
                    // console.log(prevData[indexInUpperData]);
                    const newUpdate = {
                        ...prevData[indexInUpperData],
                        ...updateData,
                    };

                    prevData[indexInUpperData] = newUpdate;
                    const newData = [...prevData];
                    return newData;
                });
            }
        }
        // console.log({ updateData });
    };

    // actions
    const copyToClipboard = () => {
        navigator.clipboard.writeText(voca);
    };

    const handleFindSimilarWords = async () => {
        const similarWords = findSimilarWords(data, voca, translate);
        setSimiles(similarWords);
        const updateData = { similar: similarWords };
        if (isNotTestMode && similarWords?.length > 0) {
            await API_VOCA.updateWord(id, updateData);
        }
    };

    const handleClickSimilarWord = async (index) => {
        const newSimiles = [...similes];
        newSimiles.splice(index, 1);

        const updateData = { similar: newSimiles };

        // Update Ui
        setSimiles(newSimiles);

        if (isNotTestMode) {
            await API_VOCA.updateWord(id, updateData);
        }

        // Update Ui
        setData((prevData) => {
            // const newData = [
            //     ...prevData,
            //     {
            //         ...prevData[indexInData],
            //         ...updateData,
            //     },
            // ];
            prevData[indexInData] = { ...prevData[indexInData], ...updateData };
            // newData.splice(indexInData, 1);
            return prevData;
        });
    };

    const handleKeydownInputSentence = async (e, setValue) => {
        if (e.keyCode === 13) {
            const value = e.target.value.trim();

            if (value) {
                const updateData = {
                    note: `<div class="sr-my-sentence"><span class="sr-symbol">&#9733;</span>${value}</div> ${wordInfo.note}`,
                };
                setWordInfo((wordInfo) => ({
                    ...wordInfo,
                    ...updateData,
                }));

                if (isNotTestMode) {
                    await API_VOCA.updateWord(id, updateData);
                }
            }

            setValue("");
        }
    };

    const handleGetExamples = async () => {
        setIsLoading(true);
        const result = await wordsApi.getWordDetail(voca);

        const examplesFormatHtmlString =
            result.map((example) => `<div>- ${example}</div>`).join(" ") || "";
        if (examplesFormatHtmlString) {
            setWordInfo((wordInfo) => {
                return {
                    ...wordInfo,
                    examples: `${
                        wordInfo.examples ? wordInfo.examples + "<br/>" : ""
                    }${examplesFormatHtmlString}`,
                };
            });
        }
        setIsLoading(false);
    };

    const handleGetSynonymsFromWordsApi = async () => {
        setIsLoading(true);
        const result = await wordsApi.getWordDetail(voca, {
            endpoint: "synonyms",
        });

        const synonymsFormatted = result.map((synonym) => {
            return {
                _typeSimilar: 3,
                typeWords: [],
                spelling: "",
                translate: "",
                voca: synonym,
            };
        });

        const update = [...synonymsFormatted, ...similes];

        setSimiles(update);
        setIsLoading(false);

        if (isNotTestMode && synonymsFormatted?.length > 0) {
            await API_VOCA.updateWord(id, { similar: update });
        }
    };

    const handleForcusExamplesAndNotes = async (e) => {
        setShowDetail(true);
        const _text = await navigator.clipboard.readText();
        const text = _text.trim().split("\n").join(" : ");
        // copyInputRef.current.value = text;
        await navigator.clipboard.writeText(text);
    };

    const handleMissedWords = async (_updateData) => {
        // console.log("in handle missed words", _updateData);
        // await setWordInfo((wordInfo) => {
        //     console.log("in set word");
        //     const handle = async () => {
        //         console.log("in handle");
        // console.log("handle", wordInfo);
        const updateData = _updateData
            ? { ..._updateData, missedWordId: _updateData.id }
            : {
                  ...wordInfo,
                  missedWordId: wordInfo.id,
                  data: null,
                  setData: null,
                  setMissedWords: null,
                  setUpperDataSearch: null,
              };
        // console.log({ updateData });
        let existed = -1;
        if (updateData.voca.toUpperCase().trim() === "GRAMMAR") {
            return false;
        }
        if (setMissedWords) {
            // console.log({ wordInfo });
            setMissedWords((missedWordsPrevData) => {
                // const newData = [...missedWordsPrevData, updateData];
                const newData = [...missedWordsPrevData];
                existed = newData.findIndex(
                    (item) =>
                        item.missedWordId === updateData.id ||
                        item.voca.toLowerCase() ===
                            updateData.voca.toLowerCase()
                );
                // console.log({ existed });

                if (existed >= 0) {
                    newData[existed] = updateData;
                    console.log("update missed word");
                    // console.log("had data");
                    // Gọi API cập nhật dữ liệu trong database ở đây nếu cần thiết
                    // await API_VOCA.updateWord(updateData.id, { ...updateData, timeAt: Date.now() }, COLLECTIONS.missedWords);
                } else {
                    newData.push(updateData);
                    console.log("push missed word");
                }
                // console.log({ missedWords: newData, length: newData.length });
                // console.log(newData);
                return newData;
            });
            // console.log({ existed });
            if (existed < 0 && isNotTestMode) {
                await API_VOCA.newWord(
                    { ...updateData, timeAt: Date.now() },
                    COLLECTIONS.missedWords
                );
            }
        }
    };

    const handleDoubleClickImage = async (e) => {
        console.log("On double click image");
        const updateData = { imageInfo: {} };
        if (isNotTestMode) {
            await API_VOCA.updateWord(id, updateData);
        }
        // target.innerHTML = highlightText;
        setWordInfo((prev) => ({ ...prev, ...updateData }));
        // console.log("on Blur");

        // Update Ui
        setData((prevData) => {
            prevData[indexInData] = {
                ...prevData[indexInData],
                ...updateData,
            };
            return prevData;
        });

        // handle UI
        // console.log({ setUpperDataSearch, indexInUpperData });
        if (setUpperDataSearch && indexInUpperData >= 0) {
            setUpperDataSearch((prevData) => {
                // console.log(prevData[indexInUpperData]);
                const newUpdate = {
                    ...prevData[indexInUpperData],
                    ...updateData,
                };

                prevData[indexInUpperData] = newUpdate;
                const newData = [...prevData];
                return newData;
            });
        }
    };

    const handleClickImageGallery = async (image, options) => {
        const updateData = { imageInfo: { ...image } };
        if (isNotTestMode) {
            await API_VOCA.updateWord(id, updateData);
        }
        // target.innerHTML = highlightText;
        setWordInfo((prev) => ({ ...prev, ...updateData }));
        // console.log("on Blur");

        // Update Ui
        setData((prevData) => {
            prevData[indexInData] = {
                ...prevData[indexInData],
                ...updateData,
            };
            return prevData;
        });

        // handle UI
        // console.log({ setUpperDataSearch, indexInUpperData });
        if (setUpperDataSearch && indexInUpperData >= 0) {
            setUpperDataSearch((prevData) => {
                // console.log(prevData[indexInUpperData]);
                const newUpdate = {
                    ...prevData[indexInUpperData],
                    ...updateData,
                };

                prevData[indexInUpperData] = newUpdate;
                const newData = [...prevData];
                return newData;
            });
        }
        options.showGallery((prev) => {
            return false;
        });
    };

    const moreActions = [
        {
            htmlSymbol: "&#10053;",
            handleClick: async (e) => {
                const currentTime = new Date().getTime();

                const timeDelay = schedule.normal[0];

                if (true) {
                    const updateData = {
                        repetitiveTime: currentTime + timeDelay, // end of time = _time[_time.length - 1]
                    };

                    // Update API
                    if (isNotTestMode) {
                        await API_VOCA.updateWord(id, updateData);
                    }

                    // Update Ui
                    setData((prevData) => {
                        const _updateData = {
                            ...prevData[indexInData],
                            ...updateData,
                        };
                        const newData = [...prevData, _updateData];
                        handleMissedWords(_updateData);
                        newData.splice(indexInData, 1);
                        return newData;
                    });
                }
            },
        },
        {
            htmlSymbol: "&#9733;",
            handleClick: () => {
                setMySentences((prev) => ({
                    ...prev,
                    isShowInput: !prev.isShowInput,
                }));
            },
        },
        {
            htmlSymbol: "&#8499;",
            handleClick: handleGetExamples,
        },
        {
            htmlSymbol: "&#9852;",
            handleClick: handleGetSynonymsFromWordsApi,
        },
        {
            htmlSymbol: "&#9850;",
            handleClick: handleFindSimilarWords,
        },
    ];

    // console.log({ wordInfo });

    // console.log({ remainingTime }, convertMilliseconds(remainingTime));
    return (
        <>
            {props.nextLevel ? (
                <></>
            ) : (
                <div
                    ref={cardItemRef}
                    className={`sr-card sr-container ${
                        showDetail && "sr-show-detail-voca"
                    } ${simple && "sr-card-simple"} ${
                        isNewWord && "sr-card-new-word"
                    }`}
                    // style="order: -57142365"
                    // style={{ order: remainingTime }}
                    data-level={level}
                >
                    <Gallery
                        data={galleryAlbums}
                        selected={wordInfo?.imageInfo?.url}
                        imageClick={handleClickImageGallery}
                        isShow={false}
                        searchValue={wordInfo?.voca}
                    />
                    <div className="sr-card-header">
                        <h2
                            id="voca"
                            className="sr-title"
                            contentEditable="true"
                            onBlur={handleOnBlur}
                            dangerouslySetInnerHTML={{
                                __html: voca?.onHighlightText(searchText),
                            }}
                        >
                            {/* {} */}
                        </h2>
                        <button
                            onClick={copyToClipboard}
                            className="sr-btn sr-html-symbol sr-html-symbol-btn sr-copy-btn"
                        >
                            &#10063;
                        </button>
                        {typeWords && typeWords.length > 0 && (
                            <div className="sr-type-word-container">
                                {typeof typeWords === "string" ? (
                                    <span className="type-words">
                                        (<span id="typeWords">{typeWords}</span>
                                        )
                                    </span>
                                ) : (
                                    typeWords.map((typeWord) => (
                                        <span className="type-words">
                                            (
                                            <span id="typeWords">
                                                {typeWord}
                                            </span>
                                            )
                                        </span>
                                    ))
                                )}
                            </div>
                        )}

                        <span className="spelling">
                            /
                            <span
                                id="spelling"
                                contentEditable="true"
                                onBlur={handleOnBlur}
                                dangerouslySetInnerHTML={{
                                    __html:
                                        spelling?.onHighlightText(searchText) ||
                                        "spelling",
                                }}
                            ></span>
                            /
                        </span>
                    </div>

                    <div>
                        {/* {!meaning && translate && (
                        <p
                            id="translate"
                            className="note translate"
                            contentEditable="true"
                            onBlur={handleOnBlur}
                        >
                            {translate}
                        </p>
                    )} */}

                        <div>
                            <span
                                id="meaning"
                                className="note meaning"
                                contentEditable="true"
                                onBlur={handleOnBlur}
                                dangerouslySetInnerHTML={{
                                    __html: meaning?.onHighlightText(
                                        searchText
                                    ),
                                }}
                            ></span>
                            <>
                                <span className="sr-meaning-flag">|</span>
                                <p
                                    id="translate"
                                    className="note sr-translate"
                                    contentEditable="true"
                                    onBlur={handleOnBlur}
                                    dangerouslySetInnerHTML={{
                                        __html:
                                            translate?.onHighlightText(
                                                searchText
                                            ) || "translate",
                                    }}
                                ></p>
                            </>
                        </div>
                    </div>

                    <div className="sr-tag-words-container">
                        {similes.map((word, index) => (
                            <button
                                className={
                                    "sr-btn sr-tag-word " +
                                    (tagClassNames[word._typeSimilar] || "")
                                }
                                data-info={`${word.typeWords} /${
                                    word.spelling || ""
                                }/ ${word.translate}`}
                                onDoubleClick={() =>
                                    handleClickSimilarWord(index)
                                }
                            >
                                {word.voca}
                            </button>
                        ))}
                    </div>

                    <div
                        style={{
                            display: "flex",
                            justifyContent: "space-between",
                        }}
                        className="sr-flex-center sr-additional-actions"
                    >
                        {remainingTime > 0 ? (
                            <p
                                class="note remaining-time"
                                onClick={() => setShowDetail(!showDetail)}
                            >
                                {convertMilliseconds(remainingTime).toString({
                                    seconds: false,
                                })}
                            </p>
                        ) : (
                            <p
                                class="note over-time"
                                onClick={() => setShowDetail(!showDetail)}
                            >
                                over time
                            </p>
                        )}
                        <div
                            style={{ flexWrap: "wrap" }}
                            className="sr-flex-center"
                        >
                            {moreActions.map((action) => (
                                <button
                                    onClick={action.handleClick}
                                    className="sr-btn sr-card-similar-words sr-html-symbol sr-html-symbol-btn"
                                    dangerouslySetInnerHTML={{
                                        __html: action.htmlSymbol,
                                    }}
                                ></button>
                            ))}
                        </div>
                    </div>

                    {mySentences.isShowInput && (
                        <InputCreateSentence
                            handleKeyDown={handleKeydownInputSentence}
                        />
                    )}

                    <div className="sr-card-note-container">
                        <div
                            id="note"
                            className="sr-note"
                            contentEditable="true"
                            onFocus={handleForcusExamplesAndNotes}
                            onBlur={handleOnBlurHighlight}
                            onKeyDown={handleKeydownHighlight}
                            dangerouslySetInnerHTML={{
                                __html: wordInfo.note,
                            }}
                        ></div>
                        <div
                            id="examples"
                            className="example-container sr-example"
                            contentEditable="true"
                            onFocus={handleForcusExamplesAndNotes}
                            onBlur={handleOnBlurHighlight}
                            onKeyDown={handleKeydownHighlight}
                            dangerouslySetInnerHTML={{
                                __html: wordInfo.examples,
                            }}
                        >
                            {/* {examples} */}
                        </div>
                    </div>
                    <div className="sr-card-items-footer">
                        {wordInfo.imageInfo && wordInfo.imageInfo?.url && (
                            <div className="sr-card-item-image-container">
                                {isShowImage && (
                                    <img
                                        className="sr-card-item-image"
                                        src={wordInfo.imageInfo.url}
                                        alt={wordInfo.imageInfo.alt}
                                        onDoubleClick={handleDoubleClickImage}
                                    />
                                )}
                                <div
                                    className="sr-card-item-image-description"
                                    onClick={() =>
                                        setIsShowImage((prev) => !prev)
                                    }
                                >
                                    Photo on{" "}
                                    <a src="https://unsplash.com">Unsplash</a>
                                </div>
                            </div>
                        )}

                        <div className="card-actions">
                            <button
                                className="sr-btn btn-outline sr-circle-btn sr-actions-delete"
                                data-color="orange"
                                onClick={handleDeleteWord}
                            >
                                &#x2715;
                            </button>
                            <button
                                className="sr-btn sr-actions-prev btn-outline sr-circle-btn"
                                onClick={handlePrevLevel}
                            >
                                &#8610;
                            </button>
                            <button
                                className="sr-btn sr-actions-next sr-circle-btn"
                                onClick={handleNextLevel}
                            >
                                &#8611;
                            </button>
                            <button
                                className="sr-btn sr-actions-next sr-circle-btn"
                                onClick={handleKeepLevel}
                            >
                                &#10070;
                            </button>
                            {isLoading && (
                                <Spinner
                                    style={{ marginLeft: "15px" }}
                                    size="50px"
                                />
                            )}
                        </div>
                    </div>
                    <div>
                        {moreRelated.similarMeaningWords.length > 0 && (
                            <>
                                <h4 className="sr-cl-primary">Mean Like:</h4>
                                <div>
                                    {moreRelated.similarMeaningWords.map(
                                        (word) => (
                                            <button
                                                style={{
                                                    userSelect: "text",
                                                    padding: "7px",
                                                }}
                                                className="sr-btn sr-bg-primary sr-cl-white"
                                            >
                                                {word.word}
                                            </button>
                                        )
                                    )}
                                </div>
                            </>
                        )}
                    </div>
                    <div>
                        {moreRelated.similarSoundWords.length > 0 && (
                            <>
                                <h4 className="sr-cl-primary">Sound Like:</h4>
                                <div>
                                    {moreRelated.similarSoundWords.map(
                                        (word) => (
                                            <button
                                                style={{
                                                    userSelect: "text",
                                                    padding: "7px",
                                                }}
                                                className="sr-btn sr-bg-secondary sr-cl-white"
                                            >
                                                {word.word}
                                            </button>
                                        )
                                    )}
                                </div>
                            </>
                        )}
                    </div>
                </div>
            )}
        </>
    );
};

const tagClassNames = [
    "sr-similar-spelling-and-meaning",
    "sr-similar-spelling",
    "sr-similar-meaning sr-bg-danger",
    "sr-word-synonym",
];

CardItem.propTypes = {};

export default CardItem;
