import React from "react";

function NavMobile({
    handleShowVocaContainer,
    handleShowFormCreate,
    handleShowProgressChart,
    handleShowTypingApp,
}) {
    return (
        <nav>
            <div className="nav-mobile">
                <button
                    className="sr-btn sr-bg-primary"
                    onClick={handleShowVocaContainer}
                >
                    Voca Container
                </button>
                <button
                    className="sr-btn sr-bg-secondary"
                    onClick={handleShowFormCreate}
                >
                    Form Create
                </button>
                <button
                    className="sr-btn sr-bg-yellow"
                    onClick={handleShowProgressChart}
                >
                    Progress Chart
                </button>
                <button
                    className="sr-btn sr-bg-black"
                    onClick={handleShowTypingApp}
                >
                    Typing App
                </button>
            </div>
        </nav>
    );
}

export default NavMobile;
