import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom/client";
import CardItem from "./CardItem";
import PrintWindow from "./PrintWindow";
import printWindowStyle from "../utils/printWindowStyle.js";

import localStorage, {
    MISSED_WORDS_KEY,
    appConfigLocalStorage,
} from "../utils/localStorage";
import API_VOCA from "../firebase/API_VOCA";
import COLLECTIONS from "../firebase/collections";
import { isNotTestMode } from "../utils/global";

const { getAllItems, clearAllItems, saveItem } = localStorage(MISSED_WORDS_KEY);

function MissedContainer({
    data: data,
    setData: setGlobalMissedWordsData,
    darkTheme,
    setDarkTheme,
    handleShowVocab,
}) {
    // const [data, setData] = useState([]);
    const [showComponent, setShowComponent] = useState(false);
    const [getWords, setGetWords] = useState({ from: 0, to: 21 });
    // console.log(data);
    useEffect(() => {
        const fetchData = async () => {
            // setIsLoading(true);
            const data = await API_VOCA.get(COLLECTIONS.missedWords, {
                by: "timeAt",
            });
            const uniqueData = findUniqueData(data);
            setGlobalMissedWordsData(uniqueData);
            // console.log({ missedWords: data });
            // setIsLoading(false);
            // console.log({ data });
        };

        const findUniqueData = (data) => {
            const result = data.filter((obj, index, arr) => {
                return arr.findIndex((o) => o.id === obj.id) === index;
            });
            return result;
        };

        if (isNotTestMode && data.length <= 0) {
            // console.log("fetch data voca container");
            fetchData();
        }
        return () => {};
    }, []);

    const handlePrint = (e) => {
        e.stopPropagation();
        if (data.length <= 0) return;
        handleShowVocab();

        const odd_root = document.querySelector(".sr-print-component");
        if (odd_root) {
            odd_root.remove();
        }

        const PRINT_ROOT = window.document.createElement("div");
        PRINT_ROOT.classList.add("sr-print-component");
        window.document.body.appendChild(PRINT_ROOT);
        const root = ReactDOM.createRoot(PRINT_ROOT);
        console.log({ darkTheme });
        root.render(
            <PrintWindow
                data={data}
                getWords={getWords}
                darkTheme={darkTheme}
            />
        );

        // component print window
        // const PRINT_ROOT = printWindow.document.createElement("div");
        // printWindow.document.body.appendChild(PRINT_ROOT);
        // const root = ReactDOM.createRoot(PRINT_ROOT);
        // root.render(<PrintWindow data={data} getWords={getWords} />);

        // printWindow.document.close();
        // printWindow.focus();
        // printWindow.print();
        // printWindow.close();
        // printWindow.onafterprint = function () {
        //     printWindow.close();
        // };
    };

    const handleClear = async (e) => {
        e.stopPropagation();
        // set data = []
        setGlobalMissedWordsData([]);
        if (isNotTestMode) {
            await API_VOCA.deleteAllDocuments(COLLECTIONS.missedWords);
        }

        // clear data in local storage
        // clearAllItems();
    };

    const handleGetQuestion = async (e) => {
        e.stopPropagation();
        if (data.length > 0) {
            const wordsString = data
                .slice(getWords.from, getWords.to)
                .map((word) => word.voca)
                .join(", ");
            // console.log({ wordsString });

            // const question = `Viết một bài báo tiếng anh bao gồm những từ sau: ${wordsString}`;
            const question = `
            Đặt câu cho những từ sau bằng tiếng việt yêu cầu liệt kê theo danh sách sau đó dịch các câu đó bằng tiếng anh ở phía bên dưới: ${wordsString}
            
            Vd cho từ Hello:
            1. Hello (int): 'xin chào, bạn có khỏe không'
               <Xin chào>: "hello, how are you"
            Vd cho từ Entrepreneur:
            2. Entrepreneur (n): "Nhà doanh nhân này đã thành công trong nhiều lĩnh vực kinh doanh."
            <Nhà doanh nhân>: "This entrepreneur has been successful in various business ventures."   
               `;

            await navigator.clipboard.writeText(question);
        }
    };

    const handleChangeTheme = () => {
        const oldConfig = appConfigLocalStorage.getAllItems();
        setDarkTheme((prev) => {
            const update = { ...oldConfig, theme: !prev };

            appConfigLocalStorage.setGlobalMissedWordsData(update);
            return !prev;
        });
    };
    return (
        <div
            style={{ display: "flex", alignItems: "center", overflow: "auto" }}
            className="sr-search-container"
        >
            <h2
                style={{ width: "fit-content", display: "inline-block" }}
                className="sr-cl-secondary"
                onClick={() => setShowComponent((prev) => !prev)}
            >
                Missed ({data.length})
            </h2>
            {/* <button
                style={{
                    padding: "10px",
                    marginLeft: "15px",
                    "--size": "38px",
                }}
                className="sr-btn sr-bg-primary sr-circle-btn"
                onClick={handleChangeTheme}
                dangerouslySetInnerHTML={{
                    __html: darkTheme ? "&#9728;" : "&#9770;",
                }}
            ></button> */}
            <button
                style={{ padding: "10px", marginLeft: "15px" }}
                className="sr-btn sr-bg-primary"
                onClick={handlePrint}
            >
                Print
            </button>
            <button
                style={{ padding: "10px", marginLeft: "15px" }}
                className="sr-btn sr-bg-secondary"
                onClick={handleClear}
            >
                Clear
            </button>
            <div className="sr-flex-center" style={{ display: "inline-flex" }}>
                <button
                    style={{
                        padding: "10px",
                        marginLeft: "15px",
                        background: "#1ac27d",
                    }}
                    className="sr-btn"
                    onClick={handleGetQuestion}
                >
                    ChatGpt
                </button>
                <div
                    className="sr-flex-center"
                    style={{
                        display: "inline-flex",
                        marginLeft: " 15px",
                        overflow: "hidden",
                        borderRadius: "999px",
                        border: "1px solid",
                    }}
                >
                    <input
                        className="sr-get-words-input"
                        type="text"
                        placeholder={getWords.from}
                        onBlur={(e) =>
                            setGetWords((prev) => ({
                                ...prev,
                                from: Number(e.target.value) || 0,
                            }))
                        }
                    />
                    <input
                        style={{ borderLeft: "1px solid #ccc" }}
                        className="sr-get-words-input"
                        type="text"
                        placeholder={getWords.to}
                        onBlur={(e) =>
                            setGetWords((prev) => ({
                                ...prev,
                                to: Number(e.target.value) || 20,
                            }))
                        }
                    />
                </div>
            </div>

            {showComponent && (
                <div className="sr-search-results">
                    {data.length > 0 &&
                        data.map((element, index) => (
                            <CardItem
                                key={element.id}
                                {...element}
                                indexInData={index}
                                setGlobalMissedWordsData={
                                    setGlobalMissedWordsData
                                }
                            />
                        ))}
                </div>
            )}
        </div>
    );
}

export default MissedContainer;
