function findSimilarWords(data, vocab, translate) {
    // let similarWords = [];
    let similarWords = data
        .map((wordInfo, index) => {
            const _vocab = vocab.toLowerCase();
            const _compareVocab = wordInfo.voca.toLowerCase();

            // Nurse == nurse ==> false
            if (_vocab === _compareVocab) {
                return false;
            }
            // find similar spelling words
            const isSimilarSpell = similarSpellWord(_vocab, _compareVocab);
            const isSimilarMeaning = similarMeaningWord(
                translate,
                wordInfo.translate
            );

            // console.log({ isSimilarMeaning, isSimilarSpell });

            // const _typeSimilar = 1;
            const _typeSimilar =
                isSimilarSpell && isSimilarMeaning
                    ? 0
                    : isSimilarSpell
                    ? 1
                    : isSimilarMeaning
                    ? 2
                    : -1;
            // const _typeSimilar = isSimilarSpell ? 1 : isSimilarMeaning ? 2 : -1;

            // _typeSimilar >= 0 && isSimilarSpell
            if (_typeSimilar >= 0) {
                // console.log({ isSimilarSpell, isSimilarMeaning });
                return {
                    id: wordInfo.id,
                    voca: wordInfo.voca,
                    translate: wordInfo.translate,
                    spelling: wordInfo.spelling,
                    typeWords: wordInfo.typeWords,
                    _typeSimilar: _typeSimilar,
                };
            }
            // if (index < 3) console.log({ compareResult });
        })
        .filter(Boolean);
    // console.log(similarWords);
    return similarWords.slice(0, 10);
}

function similarMeaningWord(meaning, compareMeaning) {
    const translateLowerCase = compareMeaning.toLowerCase();
    const inputLowerCase = meaning.toLowerCase();

    const translateWords = translateLowerCase
        .split(/[,.| ]+/)
        .reduce((acc, word) => {
            if (acc.includes(word)) {
                return acc;
            }
            return [...acc, word];
        }, []);
    const inputWords = inputLowerCase.split(/[,.| ]+/).reduce((acc, word) => {
        if (acc.includes(word)) {
            return acc;
        }
        return [...acc, word];
    }, []);

    // console.log({ translateWords, inputWords });

    let minimumMatch = 2;
    // if (translateWords.length >= 6 || inputWords.length >= 6) {
    //     minimumMatch = 3;
    // }

    // console.log({ translateWords, inputWords });

    let matchingWords = 0;

    for (let i = 0; i < inputWords.length; i++) {
        if (translateWords.includes(inputWords[i])) {
            matchingWords++;
            // console.log({ inputWords: inputWords[i] });
        }
    }

    // console.log({ matchingWords });

    return matchingWords >= minimumMatch;
}

