function similarSpellWord(_vocab, _compareVocab) {
    // const _vocab = vocab.toLowerCase();
    // const _compareVocab = compareVocab.toLowerCase();

    // if (_vocab === _compareVocab) {
    //     return false;
    // }

    let sorterVocab = "";
    let longerVocab = "";
    if (_vocab.length < _compareVocab.length) {
        sorterVocab = _vocab;
        longerVocab = _compareVocab;
    } else {
        sorterVocab = _compareVocab;
        longerVocab = _vocab;
    }

    if (sorterVocab.length <= 3) {
        return false;
    } else if (longerVocab.length - sorterVocab.length >= 5) {
        return false;
    } else if (longerVocab.includes(sorterVocab)) {
        // if word length <= : don't get
        return true;
    }

    // voca => ['vo','oc','ca'].....
    const separateResult = separateString(sorterVocab);
    const compareResult = separateResult.filter((letters) => {
        const indexOf = longerVocab.indexOf(letters);
        // if (index < 2) {
        //     console.log({ letters, indexOf, longerVocab });
        // }
        if (indexOf >= 0) {
            longerVocab = longerVocab.slice(indexOf);
            return true;
        }
    });

    // percentage 1 = 100%
    const minimumPercents = 0.6;
    // console.log({
    //     compareResult,
    //     sorterVocab,
    //     score: compareResult.length / separateResult.length,
    // });

    // - 1 means
    // if (compareResult.length / (_vocab.length - 1) >= minimumPercents) {
    //     return true;
    // }
    if (compareResult.length / separateResult.length >= minimumPercents) {
        return true;
    }
}

// 'hello' => ['he', 'el', 'll', 'lo']
function separateString(str) {
    const words = str.split(""); // Tách chuỗi thành các từ

    const result = [];
    for (let i = 0; i < words.length - 1; i++) {
        const word1 = words[i];
        const word2 = words[i + 1];
        const combined = word1 + word2; // Kết hợp 2 từ thành một chuỗi

        result.push(combined);
    }

    return result;
}

export default findSimilarWords;
