import React, { useEffect, useState } from "react";
import FormCreate from "./FormCreate";
import _wordTemplate from "../utils/wordTemplate";
import API_VOCA from "../firebase/API_VOCA";
import COLLECTIONS from "../firebase/collections";
import { isNotTestMode } from "../utils/global";
import Spinner from "./Spinner";

function NewWordList({
    wordList,
    setWordList,
    darkTheme,
    setDarkTheme,
    currentWordList,
    setCurrentWordList,
    setShowFormCreate,
    setShowVocaContainer,
    setShowProgressChart,
    setShowTypingApp,
    setShowNewWordList,
    addingWordAmount,
    setAddingWordAmount,
}) {
    const [isLoading, setIsLoading] = useState(true);
    const [listActiveIndex, setListActiveIndex] = useState(0);
    const [selectedWordIndex, setSelectedWordIndex] = useState(0);
    const [wordTemplate, setWordTemplate] = useState(_wordTemplate);
    const [addedList, setAddedList] = useState([]);
    const [options, setOptions] = useState({
        amountItemShow: 20,
        pageNum: 1,
    });

    useEffect(() => {
        if (wordList.length > 0) {
            const _word =
                selectedWordIndex < wordList[listActiveIndex]?.wordList?.length
                    ? wordList[listActiveIndex]?.wordList[selectedWordIndex]
                    : _wordTemplate;
            const word = { ..._word, index: selectedWordIndex };
            handleClickNewWord(word);
        }
    }, [selectedWordIndex, wordList, listActiveIndex]);

    useEffect(() => {
        const fetchData = async () => {
            setIsLoading(true);
            const data = await API_VOCA.get(COLLECTIONS.wordList, {
                by: "title",
            });
            setWordList(data);
            setIsLoading(false);
            // console.log({ data });
        };

        if (wordList.length <= 0) {
            // console.log("fetch data voca container");
            fetchData();
        }
        return () => {};
    }, []);

    const handleAfterSubmitForm = async (submittedNewWord) => {
        setAddedList((prev) => {
            const word = submittedNewWord.voca || submittedNewWord.vocab;
            const firstLetter = word[0];
            return {
                ...prev,
                [firstLetter]: prev[firstLetter] ? prev[firstLetter] + 1 : 1,
            };
        });
        setIsLoading(true);
        if (selectedWordIndex < 0 && submittedNewWord.voca) {
            setAddingWordAmount((prev) => prev + 1);
            setIsLoading(false);
            return;
        }
        // if(submittedNewWord.voca.trim().toLowerCase() === wordList[listActiveIndex]
        // .wordList[selectedWordIndex]
        // .vocab) {}
        const newList = { ...wordList[listActiveIndex] };

        // console.log(selectedWordIndex);

        newList.wordList?.splice(selectedWordIndex, 1);

        // console.log({ newList, last: wordList[listActiveIndex] });

        if (isNotTestMode) {
            await API_VOCA.updateWord(
                newList.id,
                newList,
                COLLECTIONS.wordList
            );
        }

        setWordList((prevData) => {
            const newData = [...prevData];
            newData[listActiveIndex] = newList;

            return newData;
        });
        if (submittedNewWord.voca) {
            setAddingWordAmount((prev) => prev + 1);
        }
        setIsLoading(false);
    };

    const handleClickList = (index) => {
        setListActiveIndex(index);
    };

    const handleClickNewWord = (word) => {
        if (word.index < 0) {
            setWordTemplate(_wordTemplate);
            // return;
        }
        const listForm = wordList[listActiveIndex].form
            ? wordList[listActiveIndex].form
            : 0;
        // const note = word.wordFamilies?.map((word) => word).join("<br>");
        // const __wordTemplate = {
        //     ..._wordTemplate,
        //     voca: word.vocab,
        //     note: note,
        // };

        const __wordTemplate = actionClickNewWord[listForm](word);

        // console.log({ __wordTemplate, index: word.index });

        setWordTemplate(__wordTemplate);
    };

    const handleShowMore = () => {
        if (
            options.amountItemShow * (options.pageNum + 1) <=
            wordList[listActiveIndex]?.wordList?.length + options.amountItemShow
        ) {
            console.log("COUNT PAGE");
            setOptions({
                ...options,
                pageNum: options.pageNum + 1,
            });
        }
    };

    const handleRemoveList = async (e, ListId) => {
        e.stopPropagation();
        // console.log({ ListId });
        if (isNotTestMode) {
            await API_VOCA.deleteWord(ListId, COLLECTIONS.wordList);
        }
        setWordList((prevData) => {
            const newWordList = [...prevData];
            newWordList.splice(listActiveIndex, 1);
            return newWordList;
        });
    };

    const actionClickNewWord = [
        (word) => {
            const note =
                word.wordFamilies?.map((word) => word).join("<br>") || "";
            const __wordTemplate = {
                ..._wordTemplate,
                voca: word.vocab,
                note: note,
            };
            return __wordTemplate;
        },
        (word) => {
            const _word = {
                ..._wordTemplate,
                ...word,
                voca: word.voca || word.vocab,
                typeWords: word?.typeWords ? word?.typeWords[0] : "",
            };
            return _word;
        },
    ];

    return (
        <div className={`sr-new-word-list ${darkTheme ? "sr-dark-mode" : ""}`}>
            <div className="sr-overlay"></div>
            <div className={`card-container sr-new-word-list-container`}>
                <div className="sr-new-word-list-wrapper">
                    <div class="sr-sidebar">
                        <ul class="sr-options">
                            {wordList?.map((list, index) => (
                                <li
                                    style={{ justifyContent: "unset" }}
                                    key={list.id + "title"}
                                    className={`sr-options-name sr-flex-center ${
                                        listActiveIndex === index
                                            ? "active"
                                            : ""
                                    }`}
                                    onClick={() => handleClickList(index)}
                                    data-title={list.title}
                                >
                                    {listActiveIndex === index && (
                                        <button
                                            style={{
                                                "--size": "15px",
                                                fontSize: "10px",
                                                marginRight: "10px",
                                            }}
                                            // style={{ color: "white" }}
                                            className="sr-btn btn-show-more sr-bg-primary sr-circle-btn sr-cl-black sr-flex-center"
                                            onClick={(e) =>
                                                handleRemoveList(e, list.id)
                                            }
                                        >
                                            {/* {index} */}
                                        </button>
                                    )}
                                    {list.title}{" "}
                                    {`(${list.wordList?.length || 0})`}
                                </li>
                            ))}
                        </ul>
                    </div>
                    <div class="sr-main-content">
                        <h6 className="sr-title">
                            <span onClick={setShowNewWordList}>
                                Danh sách các từ mới:{" "}
                                {isLoading && <Spinner size="20px" />}
                            </span>{" "}
                            <span>
                                {wordList[listActiveIndex]?.wordList?.length}
                            </span>
                            <span> / {addingWordAmount}</span>
                        </h6>{" "}
                        <ul class="sr-word-list">
                            <li
                                className={`sr-word ${
                                    selectedWordIndex === -1 ? "active" : ""
                                }`}
                                onClick={(e) => setSelectedWordIndex(-1)}
                            >
                                NULL
                            </li>
                            {wordList[listActiveIndex]?.wordList
                                ?.slice(
                                    0,
                                    options.amountItemShow * options.pageNum
                                )
                                ?.map((word, index) => {
                                    const _word = { ...word, index: index };
                                    // console.log({
                                    //     voca: word.voca,
                                    //     vocab: word.vocab,
                                    //     word,
                                    // });

                                    const firstLetter = word.vocab
                                        ? word.vocab[0]
                                        : word.voca[0];
                                    // console.log({ firstLetter: firstLetter });
                                    const passLetter =
                                        addedList[firstLetter] >= 2
                                            ? true
                                            : false;
                                    return (
                                        <li
                                            key={word.vocab}
                                            className={`sr-word ${
                                                selectedWordIndex === index
                                                    ? "active"
                                                    : ""
                                            } ${
                                                passLetter ? "pass-letter" : ""
                                            }`}
                                            onClick={(e) =>
                                                setSelectedWordIndex(index)
                                            }
                                        >
                                            <p className="sr-root-word">
                                                {word.voca || word.vocab}{" "}
                                                {index ===
                                                    selectedWordIndex && (
                                                    <span
                                                        style={{
                                                            "--size": "15px",
                                                            fontSize: "10px",
                                                        }}
                                                        className="sr-btn sr-circle-btn sr-delete-btn"
                                                        onClick={(e) => {
                                                            e.stopPropagation();
                                                            handleAfterSubmitForm(
                                                                word
                                                            );
                                                        }}
                                                    >
                                                        ✖
                                                    </span>
                                                )}
                                            </p>
                                            <p className="more-info">
                                                <small>
                                                    (
                                                    {typeof word.typeWords ===
                                                    "string"
                                                        ? word.typeWords
                                                        : word.typeWords
                                                              ?.length > 0
                                                        ? word.typeWords?.map(
                                                              (type) => type
                                                          )
                                                        : ""}
                                                    )
                                                </small>
                                                <p
                                                    style={{ width: "10px" }}
                                                ></p>
                                                <span className="sr-cl-primary">
                                                    {word.translate}
                                                </span>
                                            </p>
                                            {word.wordFamilies?.length > 0 && (
                                                <ul className="sr-word-families">
                                                    {word.wordFamilies?.map(
                                                        (word) => (
                                                            <li
                                                                key={
                                                                    word +
                                                                    "word-families"
                                                                }
                                                            >
                                                                {word}
                                                            </li>
                                                        )
                                                    )}
                                                </ul>
                                            )}
                                            {index === selectedWordIndex && (
                                                <li
                                                    style={{ width: "100%" }}
                                                    className={`sr-word sr-btn sr-bg-yellow ${
                                                        selectedWordIndex === -1
                                                            ? "active"
                                                            : ""
                                                    }`}
                                                    onClick={(e) =>
                                                        setSelectedWordIndex(-1)
                                                    }
                                                >
                                                    NULL
                                                </li>
                                            )}
                                        </li>
                                    );
                                })}
                            <li></li>{" "}
                        </ul>
                        {options.amountItemShow * (options.pageNum + 1) <=
                            wordList[listActiveIndex]?.wordList?.length +
                                options.amountItemShow && (
                            <button
                                style={{ color: "white" }}
                                className="sr-btn btn-show-more sr-bg-yellow sr-circle-btn"
                                onClick={handleShowMore}
                            >
                                &#8480;
                            </button>
                        )}
                    </div>
                </div>
                <FormCreate
                    positionFixed={false}
                    data={currentWordList}
                    setData={setCurrentWordList}
                    wordTemplate={wordTemplate}
                    afterSubmit={handleAfterSubmitForm}
                    setShowFormCreate={setShowFormCreate}
                    setShowVocaContainer={setShowVocaContainer}
                    setShowProgressChart={setShowProgressChart}
                    setShowTypingApp={setShowTypingApp}
                    setShowNewWordList={setShowNewWordList}
                    noHandleGetSpelling={true}
                    closeFormAfterSubmit={false}
                />
            </div>
        </div>
    );
}

export default NewWordList;
