const API_VOCA = {
    async get(COLLECTION = COLLECTIONS.default, sort = true) {
        try {
            let querySnapshot = [];
            const typeSort = typeof sort == "object";
            if (sort == true || typeSort) {
                querySnapshot = await getDocs(
                    query(
                        collection(db, COLLECTION),
                        orderBy(typeSort ? sort.by : "repetitiveTime", "asc")
                    )
                );
            } else {
                querySnapshot = await getDocs(collection(db, COLLECTION));
            }

            const documents = [];
            querySnapshot.forEach((doc) => {
                documents.push({ ...doc.data(), id: doc.id });
            });
            console.log("+++++++++ GET DOCUMENTS SUCCESS ++++++++++");
            // console.log({ documents });
            return documents;
        } catch (error) {
            alert("GET DATA FAILED: ", error);
        }
    },

    async newWord(newWord, COLLECTION = COLLECTIONS.default) {
        // console.log("Add new word: ", newWord);
        // try {
        try {
            // console.log("hello");
            const docRef = await addDoc(collection(db, COLLECTION), newWord);
            console.log("++++++++++++ ADD NEW WORD SUCCESS ++++++++++++++");
            console.log("Document written with ID: ", docRef.id);
            return docRef.id;
        } catch (e) {
            console.log(e);
            alert("ADD NEW WORD FAIL ", e);
        }
    },

    async updateWord(documentId, data, COLLECTION = COLLECTIONS.default) {
        // console.log({ documentId: documentId, data: data });
        try {
            console.log({ documentId });
            const docRef = doc(db, COLLECTION, documentId);
            await updateDoc(docRef, data);
            console.log("+++++++ UPDATE NEW WORD SUCCESS +++++++++");
        } catch (error) {
            alert("Error UPDATE document: ", error);
            console.log(error);
        }
    },

    async deleteWord(
        documentId = "H4mqf7V9Mx0mo3kMBVwH",
        COLLECTION = COLLECTIONS.default
    ) {
        try {
            const docRef = doc(db, COLLECTION, documentId);
            await deleteDoc(docRef);
            console.log("++++++++++ DELETE WORD SUCCESS ++++++++++");
        } catch (error) {
            alert("DELETE WORD FAILED: ", error);
        }
    },

    async deleteAllDocuments(COLLECTION) {
        try {
            const querySnapshot = await getDocs(
                query(collection(db, COLLECTION))
            );

            // Tạo một mảng promises để xóa từng tài liệu một
            const deletePromises = [];
            querySnapshot.forEach((doc) => {
                deletePromises.push(deleteDoc(doc.ref));
            });

            // Chờ cho tất cả các promises xóa tài liệu hoàn tất
            await Promise.all(deletePromises);

            console.log("Xóa tất cả tài liệu thành công!");
        } catch (error) {
            console.error("Lỗi khi xóa tài liệu:", error);
        }
    },
};

export default API_VOCA;
