const baseUrl = "https://api.dictionaryapi.dev/api/v2/entries/en/";

const typeWords = {
    noun: "n",
    verb: "v",
    adverb: "adv",
    adjective: "a",
};

const fetchWordInfo = {
    async get(word, callback) {
        // console.log(word);
        // console.log(baseUrl + word);
        // console.log({ word });
        let pronunciation = "";
        const [
            dictionary,
            [verbForm, phrases],
            _wordsApi,
            spellLike,
            similarMeaning,
        ] = await Promise.all([
            fetch(baseUrl + word),
            this._getVerbFormAndPhrase(word),
            wordsApi.get({ word }),
            getDatamuse(word, "sp"),
            getDatamuse(word, "ml"),
        ]);

        if (!_wordsApi.pronunciation?.all && word.split(" ").length >= 2) {
            // console.log("many words");
            const promises = word
                .split(" ")
                .map((_word) => wordsApi.get({ word: _word }));
            const data = await Promise.all(promises);

            pronunciation = data
                .map(
                    (_data) =>
                        _data.pronunciation?.all || _data.pronunciation || ""
                )
                .join(" ");
        }

        // console.log({ spellLike });

        const data = await dictionary.json();
        // console.log({ _wordsApi, data, wordnik: [verbForm, phrases] });
        // const response = await fetch(baseUrl + word);
        // const data = await response.json();

        // const [verbForm, phrases] = await this._getVerbFormAndPhrase(word);
        // console.log({ data });
        if ((data && data.length > 0) || pronunciation) {
            const phonetic =
                pronunciation ||
                _wordsApi.pronunciation?.all ||
                (data[0].phonetic &&
                    data[0].phonetic?.replace(/[\[\]+=\-_:\/]/g, "")) ||
                "";
            // console.log({ phonetic, _wordsApi: _wordsApi.pronunciation?.all });
            const meaning =
                _wordsApi?.results?.length > 0
                    ? _wordsApi?.results[0].definition
                    : data[0]?.meanings && data[0]?.meanings.length > 0
                    ? data[0]?.meanings[0]?.definitions[0]?.definition
                    : "";
            const typeWord =
                (pronunciation &&
                    [...new Set(wordsApi?._getTypeWords(_wordsApi))].join(
                        " "
                    )) ||
                (data[0]?.meanings &&
                    data[0]?.meanings.length > 0 &&
                    typeWords[data[0]?.meanings[0]?.partOfSpeech]) ||
                "";
            // console.log(spellLike.slice(0, 5));
            return {
                ...data[0],
                phonetic,
                meaning,
                typeWord: typeWord.trim(),
                spellLike: spellLike.slice(1, 5) || [],
                similarMeaning: similarMeaning.slice(0, 4) || [],

                verbForm,
                phrases,
            };
        }
        return {};
    },

    // API Wordnik
    async _getVerbFormAndPhrase(word) {
        const apiKey = "5zpao24y4qxsjz3ztg5l5fr4z2opf8sh8y6w0xx1qctbzla1n";
        const baseURL = "https://api.wordnik.com/v4/word.json";

        try {
            const [relatedWords, phrases] = await Promise.all([
                fetch(`${baseURL}/${word}/relatedWords?api_key=${apiKey}`),
                fetch(`${baseURL}/${word}/phrases?api_key=${apiKey}`),
            ]);

            const relatedWordsData = await relatedWords.json();
            const phrasesData = await phrases.json();
            if (relatedWordsData?.message || phrasesData?.message)
                return [[], []];

            // console.log({
            //     relatedWords: relatedWordsData,
            //     phrases: phrasesData,
            // });

            const _verbForm = relatedWordsData.filter((relationship) => {
                return relationship.relationshipType === "verb-form";
            });
            const verbForm = _verbForm.length > 0 ? _verbForm[0].words : false;

            // console.log({ verbForm });

            const somePhrases = phrasesData.slice(0, 5);
            // console.log({ somePhrases });

            // console.log([verbForm, somePhrases]);

            return [verbForm, somePhrases];

            // Xử lý dữ liệu và thực hiện các thao tác tiếp theo
        } catch (error) {
            console.error("Đã xảy ra lỗi:", error);
            return [false, false];
        }
    },
};

export const getDatamuse = async (word, sl) => {
    const baseURL = "https://api.datamuse.com/words";
    const slag = `?${sl}=${word}`;

    try {
        const response = await fetch(baseURL + slag);
        const result = await response.json();
        // console.log(result);
        return result || [];
    } catch (error) {
        console.error(error);
    }
};

export const wordsApi = {
    async get(option) {
        const { word = "", endpoint = "" } = option;
        const url = `https://wordsapiv1.p.rapidapi.com/words/`;
        const API_KEY = "6a5a523df8msh510d7c9ce044633p1fb096jsnb3471c934428";
        const options = {
            method: "GET",
            headers: {
                "X-RapidAPI-Key": API_KEY,
                "X-RapidAPI-Host": "wordsapiv1.p.rapidapi.com",
            },
        };

        try {
            const response = await fetch(`${url}${word}/${endpoint}`, options);
            const result = await response.json();
            // console.log(result);
            return result;
        } catch (error) {
            console.error(error);
        }
    },

    async getWordDetail(word, options) {
        const { endpoint = "examples", limitResults = 5 } = options || {};

        const _result = await this.get({ word, endpoint });

        const result =
            (_result[endpoint] && _result[endpoint].slice(0, limitResults)) ||
            [];

        return result;
    },

    // async getSynonyms(word, options) {
    //     const { limitResults = 5 } = options || {};
    //     const endpoint = "synonyms";
    //     // console.log({ limitResults });
    //     const result = await this.get({ word, endpoint });

    //     const examples = result?.synonyms.slice(0, limitResults) || [];

    //     return examples;
    // },

    _getTypeWords(data) {
        const result = data?.results?.map((result) => {
            return typeWords[result.partOfSpeech];
        });
        return result;
    },
};

export default fetchWordInfo;
