var _localStorage = function (key, options = {}) {
    // Private function to get data from local storage
    const { typeOf = [] } = options;
    function getData() {
        var data = localStorage.getItem(key);
        return JSON.parse(data) || typeOf;
    }

    // Private function to set data in local storage

    // Public functions
    return {
        setData: function (data) {
            // console.log({ data });
            localStorage.setItem(key, JSON.stringify(data));
        },
        // Create or update an item in local storage
        saveItem(updateItem) {
            // console.log({ updateItem: updateItem });
            var data = getData() || typeOf;
            // console.log({ data });
            let index = data.findIndex((item) => item.id === updateItem.id);
            // console.log({ index });
            if (index >= 0) {
                data[index] = updateItem;
            } else {
                data.push(updateItem);
            }

            this.setData(data);
        },

        // Read an item from local storage
        getItem: function (itemId) {
            var data = getData();
            return data ? data[itemId] : null;
        },

        // Read all items from local storage
        getAllItems: function () {
            var data = getData();
            // console.log({ key, typeOf, data });
            // console.log("getAllItems", data);
            return data;
            // return data ? Object.values(data) : typeOf;
        },

        // Update an item in local storage
        updateItem: function (item) {
            var data = getData();
            if (data && data[item.id]) {
                data[item.id] = item;
                this.setData(data);
            }
        },

        // Delete an item from local storage
        deleteItem: function (itemId) {
            var data = getData();
            if (data && data[itemId]) {
                delete data[itemId];
                this.setData(data);
            }
        },

        // Clear all items from local storage
        clearAllItems: function () {
            // console.log("clearAllItems");
            localStorage.removeItem(key);
        },
    };
};

export const MISSED_WORDS_KEY = "SR_MISSED_WORDS";
export const SR_CONFIG_KEY = "SR_CONFIGS";

export const appConfigLocalStorage = _localStorage(SR_CONFIG_KEY, {
    typeOf: {},
});

// const { getAllItems, clearAllItems } = localStorage("SR_MISSED_WORDS");

export default _localStorage;
