import React from "react";
import { useState } from "react";

function InputCreateSentence({ handleKeyDown }) {
    const [value, setValue] = useState("");
    return (
        <div style={{ display: "flex" }}>
            <input
                type="text"
                className="sr-create-sentence-input"
                value={value}
                onChange={(e) => setValue(e.target.value)}
                onKeyDown={(e) => {
                    handleKeyDown(e, setValue);
                }}
                // placeholder="&reg;"
            />
        </div>
    );
}

export default InputCreateSentence;
