import React, { useEffect, useRef, useState } from "react";
import schedule from "../utils/schedule";
import API_VOCA from "../firebase/API_VOCA";
import chromeExtension from "../chromeExtension/chromeExtension";
import fetchWordInfo from "../dictionaryApi/dictionaryapi";
import textToHighlight from "../utils/textToHighlight";
import SearchField from "./SearchField";
import { isNotTestMode } from "../utils/global";
import findSimilarWords from "../utils/findSimilarWords";
import replaceSpecialCharacters from "../utils/replaceSpecialCharacters";
import Spinner from "./Spinner";
import googlePhotos from "../utils/googlePhotos";
import Gallery from "./Gallery";
import _wordTemplate from "../utils/wordTemplate";
import convertMilliseconds from "../utils/convertMilliseconds";
import { extensionActions } from "../extension/constances";

let indexWord = -1;
function FormCreate({
    setShowFormCreate,
    setShowVocaContainer,
    setShowProgressChart,
    setShowTypingApp,
    data,
    setData,
    positionFixed = true,
    wordTemplate = _wordTemplate,
    afterSubmit,
    setShowNewWordList,
    noHandleGetSpelling,
    closeFormAfterSubmit = true,
}) {
    const [wordInfo, setWordInfo] = useState(wordTemplate);
    const [isLoading, setIsLoading] = useState(false);
    const [images, setImages] = useState([]);
    // const [level, setLevel] = useState(0);
    const vocaInputRef = useRef(null);
    const translateFieldRef = useRef(null);

    // total level 1 - 7
    const totalLevel = schedule.normal.length;
    let levelArray = [];
    for (let i = 0; i < totalLevel; i++) {
        levelArray.push(i);
    }

    useEffect(() => {
        const timerId = setTimeout(() => {
            // &&
            //     (!wordInfo.examples &&
            //     !wordInfo.note)
            if (wordInfo.voca.trim() !== "") {
                // console.log(wordInfo.voca.trim());
                // console.log(wordInfo);
                handleGetSpelling();
            }
        }, 800);

        return () => {
            clearTimeout(timerId);
        };
    }, [wordInfo.voca]);
    // console.log(wordInfo);

    useEffect(() => {
        if (vocaInputRef.current) {
            vocaInputRef.current.focus();
        }
    }, []);

    useEffect(() => {
        setWordInfo(wordTemplate);
        vocaInputRef.current.value = wordTemplate.voca;
        if (!wordTemplate.typeWords) {
            handleGetSpelling();
        }
    }, [wordTemplate]);

    // useEffect(() => {
    //     console.log({ wordInfo, level });
    //     return () => {};
    // }, [wordInfo, level]);

    // add event handle translate enter
    // useEffect(() => {
    //     const handleKeydown = (e) => {
    //         if (e.keyCode == "13") {
    //             handleSubmit();
    //         }
    //     };

    //     const translateField = translateFieldRef.current;
    //     if (translateField) {
    //         translateField.addEventListener("keydown", handleKeydown);
    //     }

    //     return () => {
    //         translateField.removeEventListener("keydown", handleKeydown);
    //     };
    // }, []);

    // add event handle highlight example field

    // add event selection
    useEffect(() => {
        const getSelection = async function () {
            const selection = window.getSelection().toString();

            if (selection.length > 0) {
                // console.log("Đã bôi đen văn bản: ", selection);
                const _wordInfo = await fetchWordInfo.get(selection);

                setWordInfo((prev) => {
                    if (prev.voca.trim()) {
                        return prev;
                    }
                    let update = { ...prev, voca: selection };
                    if (_wordInfo) {
                        // update = { ...prev, voca: selection };
                        const spelling = _wordInfo.phonetic || "";
                        const meaning = _wordInfo.meaning || "";
                        const typeWord = _wordInfo.typeWord || "";
                        // console.log({ prev, update });
                        vocaInputRef.current.value = selection;

                        update = {
                            ...update,
                            spelling,
                            typeWords: typeWord,
                            meaning: meaning,
                        };
                    }
                    return update;
                });
            }
        };

        getSelection();

        document.addEventListener("mouseup", getSelection);

        return () => {
            document.removeEventListener("mouseup", getSelection);
        };
    }, []);

    const handleSubmit = async () => {
        if (wordInfo.voca.length <= 0) {
            alert("WORD EMPTY");
            return;
        }
        let newWord = {
            id: wordInfo.id,
            voca: wordInfo.voca,
            spelling: wordInfo.spelling,
            meaning: wordInfo.meaning,
            translate: wordInfo.translate,
            examples: wordInfo.examples,
            note: wordInfo.note,
            typeWords: wordInfo.typeWords,
            time: wordInfo.time,
            similar: wordInfo.similar,
            opposite: wordInfo.opposite,
            level: wordInfo.level,
            repetitiveTime: wordInfo.repetitiveTime,
        };
        // console.log({ prevWordInfo: wordInfo });
        if (
            typeof newWord.typeWords === "string" &&
            newWord.typeWords.length > 0
        ) {
            // console.log(input.value.trim().split(" "));
            newWord.typeWords = newWord.typeWords.split(" ") || [];
        } else {
            newWord.typeWords = newWord.typeWords || [];
        }

        let oldWord = data?.filter((word) => word.id === wordInfo.id)[0] || {};
        // if (oldWord) console.log("Is old word");

        if (!newWord.id || oldWord.level !== wordInfo.level) {
            // console.log("handle time");
            newWord.time = levelToTime(wordInfo.level);
            const currentTime = new Date().getTime();
            newWord.repetitiveTime =
                !wordInfo.id && wordInfo.level === 0
                    ? currentTime + -86400000
                    : newWord.time[wordInfo.level + 1];
            // console.log({ currentTime, time: wordInfo.level + 1 });
            // console.log("handle repetitiveTime", {
            //     repetitiveTime: newWord.repetitiveTime,
            //     oldWord,
            // });
        }

        newWord.spelling = wordInfo.spelling.replace(/[\[\]+=\-_:\/]/g, "");

        if (newWord.id) {
            // console.log({ id: newWord.id });
            if (isNotTestMode) {
                await API_VOCA.updateWord(newWord.id, newWord);
            }
            // target.innerHTML = highlightText;
            setWordInfo((prev) => ({ ..._wordTemplate }));
            // console.log("on Blur");
            // Update Ui
            setData((prevData) => {
                // const newData = [...prevData];
                prevData[indexWord] = {
                    ...prevData[indexWord],
                    ...newWord,
                };

                chromeExtension.sendMessage({
                    action: extensionActions.addNewVocabInData,
                    payload: prevData,
                });
                return prevData;
            });

            // console.log({ newWord: newWord });
        } else {
            let newWordId = Math.random();
            if (isNotTestMode) {
                newWordId = await API_VOCA.newWord(newWord);
            }
            const newWordDatabase = { id: newWordId, ...newWord };
            setData((prevData) => {
                // if (prevData.length > 0) {
                prevData.unshift(newWordDatabase);
                // console.log({ newData: prevData });
                // const newData = [...prevData];
                return prevData;
                // }
                // return prevData;
            });

            chromeExtension.sendMessage({
                action: extensionActions.addNewVocabInData,
                payload: newWordDatabase,
            });
        }

        // console.log({ newWordDatabase });
        // console.log({ newWord });
        // // Close form
        // setShowFormCreate();
        setImages([]);
        setWordInfo(_wordTemplate);
        // focus input voca
        // console.log({ vocaInputRef: vocaInputRef.current });
        vocaInputRef.current.value = "";
        vocaInputRef.current.focus();

        if (afterSubmit) {
            afterSubmit(newWord);
        }

        if (closeFormAfterSubmit) {
            setShowFormCreate(false);
        }

        // CREATE ALARM NOTIFICATION
        chromeExtension.sendMessage({
            vocaAlarm: true,
            alarmName: chromeExtension.reminder,
            data: newWord,
        });

        function levelToTime(level) {
            const currentTime = new Date().getTime();
            let timeArray = [currentTime];
            // console.log({ level });
            for (let i = 0; i <= level; i++) {
                // console.log(i);
                timeArray.push(currentTime + schedule.normal[i]);
            }
            return timeArray;
        }
    };

    const handleKeyDownSubmit = (e) => {
        if (e.key === "Enter") {
            handleSubmit();
        }
    };

    const handleOnChange = async (e) => {
        e.preventDefault();
        e.stopPropagation();
        const type = e.target.id;
        let value = e.target.value;

        let update = {};

        update = {
            ...wordInfo,
            [type]: value,
        };

        setWordInfo(update);
        // console.log("some thing");
        // console.log({ update });
    };
    const handleOnChange2 = async (e) => {
        console.log("onchange 2");
        let value = e.target.value;

        let update = {};

        update = {
            ...wordInfo,
            voca: value,
        };

        setWordInfo(update);
        // console.log({ update });
    };

    const handleChooseLevel = (e) => {
        const _level = Number(e.target.innerText);
        // console.log({ level: _level });
        setWordInfo({
            ...wordInfo,
            level: _level,
        });
    };

    const handleShowProgressChart = () => {
        setShowFormCreate(false);
        setShowProgressChart();
        setShowNewWordList(false);
    };
    const handleShowVocaContainer = () => {
        setShowFormCreate(false);
        setShowVocaContainer();
        setShowNewWordList(false);
    };
    const handleShowTypingApp = () => {
        setShowFormCreate(false);
        setShowTypingApp();
        setShowNewWordList(false);
    };
    const handleShowNewWordList = () => {
        setShowFormCreate(false);
        setShowNewWordList();
    };

    const handleGetSpelling = async (e) => {
        setIsLoading(true);
        const type = vocaInputRef.current?.id || "voca";
        const value = wordInfo.voca || vocaInputRef.current?.value.trim() || "";
        if (wordInfo.hasNote) {
            setIsLoading(false);
            return;
        }
        // console.log({ value });
        // if (value === wordInfo.voca) return;
        // console.log("sr-app: handle get info for", value);
        if (!value) {
            setImages([]);
        }
        // console.log({ value, word: wordInfo.voca.trim() });
        // if (value === wordInfo.voca.trim()) {
        //     return;
        // }
        const _wordInfo = await fetchWordInfo.get(value);
        let similarWords = [];
        // if (data.length > 0) {
        //     similarWords = findSimilarWords(data, value);
        // }

        // let update = {
        //     ...wordInfo,
        // };
        // console.log({ _wordInfo: _wordInfo });
        if (_wordInfo) {
            // console.log(_wordInfo.phonetic);
            const spelling = _wordInfo.phonetic || "";
            const meaning = _wordInfo.meaning || "";
            const typeWord = wordInfo.typeWords
                ? wordInfo.typeWords
                : _wordInfo.typeWord || "";

            const spellLike =
                _wordInfo.spellLike?.length > 0
                    ? `<div>Sound : ${_wordInfo.spellLike
                          .map((word) => word.word + " ")
                          .join(" / ")}</div>`
                    : "";
            const similarMeaning =
                _wordInfo.similarMeaning?.length > 0
                    ? `<div>Meaning : ${_wordInfo.similarMeaning
                          .map((word) => word.word + " ")
                          .join(" / ")}</div>`
                    : "";

            // console.log({ spellLike, spellLike: wordInfo.spellLike });

            const verbForm = _wordInfo.verbForm
                ? _wordInfo.verbForm.some(
                      (verb) =>
                          !verb.toLowerCase().includes(value.toLowerCase()) &&
                          !verb.includes("ing")
                  ) && _wordInfo.verbForm.length > 0
                    ? `<div class='sr-note-related sr-note-verbform'><span class='sr-cl-primary'>Verb form :</span>
            <div>${_wordInfo.verbForm.map(
                (verb, index) =>
                    `${verb} ${
                        index < _wordInfo.verbForm.length - 1 ? "- " : ""
                    }`
            )}</div></div>`.replace(/[,]/g, "")
                    : ""
                : "";
            //     const phrases =
            //         _wordInfo.phrases.length > 0
            //             ? `<div class='sr-note-related sr-note-phrases'><span class='sr-cl-primary'>Phrases :</span>
            // <div>${_wordInfo.phrases.map(
            //     (verb, index) =>
            //         `${verb.gram1 + " " + verb.gram2} : ${
            //             index < _wordInfo.phrases.length ? "/" : ""
            //         } `
            // )}</div></div>`.replace(/[,]/g, "")
            //             : "";

            const note = `
            ${verbForm}
            ${spellLike}
            ${similarMeaning}
            ${wordTemplate.note}
            `;
            // console.log({ spelling, meaning, typeWord, note });

            // const _photoUrl = await googlePhotos.searchImages(value);
            // let photoUrl = "";
            // if (_photoUrl) {
            //     photoUrl = _photoUrl[0];
            //     setImages(_photoUrl);
            // }
            setWordInfo((prevWordInfo) => {
                const update = {
                    ...prevWordInfo,
                    [type]: value,
                    voca: vocaInputRef.current?.value.trim(),
                    spelling,
                    meaning,
                    note,
                    typeWords: typeWord,
                    similar: similarWords,
                    // imageInfo: { url: photoUrl },
                };

                return update;
            });

            // const _photoUrl = [""] || (await googlePhotos.searchImages(value));
            // let photoUrl = {};
            // if (_photoUrl) {
            //     photoUrl = _photoUrl[0];
            //     setImages(_photoUrl);
            // }
            // setWordInfo((prevWordInfo) => {
            //     const update = {
            //         ...prevWordInfo,
            //         imageInfo: photoUrl,
            //     };

            //     return update;
            // });
        }
        setIsLoading(false);
    };

    const handleHighlightAndGetWordInfo = async (e, isStop = false) => {
        e.stopPropagation();
        setIsLoading(true);
        const _plainText = e.target.innerHTML;
        const plainText = replaceSpecialCharacters(
            _plainText,
            wordInfo.voca,
            wordInfo.translate
        );

        // console.log({ plainText });

        const highlightText = await textToHighlight(plainText);

        setWordInfo((prev) => ({
            ...prev,
            [e.target.id]: replaceSpecialCharacters(
                highlightText,
                wordInfo.voca,
                wordInfo.translate
            ),
        }));
        if (!isStop) {
            setTimeout(() => {
                handleHighlightAndGetWordInfo(e, true);
            }, 100);
        }
        setIsLoading(false);
    };

    const handleKeydownHighlight = (e) => {
        // e.preventDefault();
        e.stopPropagation();
        // console.log(e.key);
        if (e.keyCode == "50") {
            handleHighlightAndGetWordInfo(e);
            e.target.focus();
            // var range = document.createRange(); // Tạo một đối tượng Range
            // range.selectNodeContents(e.target); // Chọn toàn bộ nội dung của thẻ div
            // range.collapse(false); // Di chuyển con trỏ chuột đến cuối của văn bản

            // var selection = window.getSelection(); // Lấy đối tượng Selection
            // selection.removeAllRanges(); // Xóa bỏ tất cả các range hiện tại
            // selection.addRange(range); // Thêm range mới đã được thiết lập
            // console.log({ range, selection });
        }
        // return false;
        // else if (e.key === " ") {
        //     e.stopPropagation(); // Ngăn chặn hành vi mặc định của phím cách
        //     e.preventDefault();
        //     // Xử lý sự kiện khi nhấn phím cách tại đây
        //     return false;
        // } else if (e.key === "a" || e.key === "d") {
        //     e.stopPropagation(); // Ngăn chặn hành vi mặc định của phím cách
        //     e.preventDefault();
        //     // Xử lý sự kiện khi nhấn phím cách tại đây
        //     // e.stopPropagation();
        //     return true;
        // }
        // else if (e.keyCode == 51) {
        //     const inputElement = e.target;
        //     var startPos = inputElement.selectionStart; // Vị trí bắt đầu của vùng đã chọn trong input
        //     var endPos = inputElement.selectionEnd; // Vị trí kết thúc của vùng đã chọn trong input

        //     var inputValue = inputElement.innerHTML; // Giá trị hiện tại của input

        //     // Chèn văn bản mới vào vị trí đã chọn trong input
        //     var newValue =
        //         inputValue.substring(0, startPos) +
        //         wordInfo.voca +
        //         inputValue.substring(startPos, endPos);

        //     e.target.innerHTML = newValue.replace("#", "");

        //     var len = inputElement.value.length;

        //     // Mostly for Web Browsers
        //     if (inputElement.setSelectionRange) {
        //         inputElement.focus();
        //         inputElement.setSelectionRange(len, len);
        //     } else if (inputElement.createTextRange) {
        //         var t = inputElement.createTextRange();
        //         t.collapse(true);
        //         t.moveEnd("character", len);
        //         t.moveStart("character", len);
        //         t.select();
        //     }
        // } else if (e.keyCode == 52) {
        //     const inputElement = e.target;
        //     var startPos = inputElement.selectionStart; // Vị trí bắt đầu của vùng đã chọn trong input
        //     var endPos = inputElement.selectionEnd; // Vị trí kết thúc của vùng đã chọn trong input

        //     var inputValue = inputElement.innerHTML; // Giá trị hiện tại của input

        //     // Chèn văn bản mới vào vị trí đã chọn trong input
        //     var newValue =
        //         inputValue.substring(0, startPos) +
        //         wordInfo.translate +
        //         inputValue.substring(startPos, endPos);

        //     e.target.innerHTML = newValue.replace("$", "");
        //     var len = inputElement.value.length;

        //     // Mostly for Web Browsers
        //     if (inputElement.setSelectionRange) {
        //         inputElement.focus();
        //         inputElement.setSelectionRange(len, len);
        //     } else if (inputElement.createTextRange) {
        //         var t = inputElement.createTextRange();
        //         t.collapse(true);
        //         t.moveEnd("character", len);
        //         t.moveStart("character", len);
        //         t.select();
        //     }
        // }
    };

    const handleFocus = async () => {
        const _text = await navigator.clipboard.readText();
        const text = _text.trim().split("\n").join(" : ");
        // console.log(text);
        await navigator.clipboard.writeText(text);
    };

    const handleImageClick = (image) => {
        // console.log({ image });
        setWordInfo((prev) => ({
            ...prev,
            imageInfo: { ...image },
        }));
    };

    const SearchItem = (props) => {
        // console.log({ types: props.typeWords });
        const currentTime = new Date().getTime();
        const remainingTime = props.repetitiveTime - currentTime;
        // console.log({ props, remainingTime });
        // const isNewWord = time?.length <= 2 ? true : false;

        const handleClick = () => {
            const _wordInfo = {
                id: props.id,
                voca: props.voca,
                spelling: props.spelling,
                meaning: props.meaning,
                translate: props.translate,
                examples: `${props.examples} ${
                    wordInfo.examples.trim()
                        ? `<br>===New Info===<br> ${wordInfo.examples}`
                        : ""
                }`,
                note: `${props.note} ${
                    wordInfo.note.trim()
                        ? `<br>===New Info===<br> ${wordInfo.note}`
                        : ""
                }`,
                typeWords: props.typeWords?.map((type) => type).join(" "),
                time: props.time,
                similar: props.similar,
                opposite: props.opposite,
                level: props.level,
                repetitiveTime: props.repetitiveTime,
                hasNote: true,
            };
            setWordInfo(_wordInfo);
            indexWord = props.indexInUpperData;
            // if (vocaInputRef.current) {
            //     vocaInputRef.current.value = props.voca;
            // }
        };
        return (
            <div
                className={
                    "sr-search-item " + (props.formItemActive ? "active" : "")
                }
                data-level={props.level}
                onClick={handleClick}
            >
                <p>
                    <span>{props.voca}</span>{" "}
                    <span>
                        {typeof props.typeWords !== "string" &&
                            props?.typeWords?.map((type) => `(${type})`)}
                    </span>{" "}
                    <span className="sr-cl-yellow">{props.translate}</span>
                </p>
                <span className="sr-simple-search-time">
                    {convertMilliseconds(remainingTime).toString({
                        minutes: false,
                        seconds: false,
                    })}
                </span>
            </div>
        );
    };

    const handleClearForm = (e) => {
        e.stopPropagation();
        setWordInfo({ ..._wordTemplate });
        if (vocaInputRef.current) {
            vocaInputRef.current.value = "";
        }
    };

    return (
        <div>
            {/* <button onClick={getSelection}>Run</button> */}
            <div
                style={{ flexDirection: "column" }}
                className={`sr-container form-container ${
                    positionFixed ? "fixed" : ""
                }`}
            >
                <Gallery
                    selected={wordInfo?.imageInfo?.url}
                    imageClick={handleImageClick}
                    searchValue={wordInfo?.voca}
                />
                {data.length > 0 && (
                    <SearchField
                        searchValue={wordInfo?.voca}
                        data={data}
                        setData={setData}
                        simpleSearch={true}
                        showTitle={false}
                        componentItem={SearchItem}
                        formCreateItemActiveId={wordInfo.id}
                    />
                )}
                {isLoading && <Spinner size="10px" />}
                <div style={{ display: "flex" }} className="sr-form-wrapper">
                    <div className="wrap-container full">
                        <div className="my-wrapper">
                            <div className="form-input-wrapper">
                                <input
                                    ref={vocaInputRef}
                                    tabIndex="1"
                                    type="text"
                                    id="voca"
                                    placeholder="word"
                                    value={wordInfo.voca}
                                    onChange={handleOnChange}
                                    // onBlur={handleGetSpelling}
                                />
                                {wordInfo.voca && (
                                    <span
                                        onClick={handleClearForm}
                                        className="sr-btn sr-circle-btn sr-form-clear"
                                    >
                                        ✖
                                    </span>
                                )}
                            </div>
                            <div className="form-input-wrapper">
                                <input
                                    ref={translateFieldRef}
                                    tabIndex="2"
                                    type="text"
                                    value={wordInfo.translate}
                                    id="translate"
                                    placeholder="translate"
                                    onChange={handleOnChange}
                                    onKeyDown={handleKeyDownSubmit}
                                />
                            </div>
                            <div className="form-input-wrapper">
                                <input
                                    tabIndex="3"
                                    type="text"
                                    value={wordInfo.typeWords}
                                    id="typeWords"
                                    placeholder="type"
                                    onChange={handleOnChange}
                                    onKeyDown={handleKeyDownSubmit}
                                />
                            </div>
                            <div className="form-input-wrapper">
                                <input
                                    tabIndex="6"
                                    type="text"
                                    value={wordInfo.spelling}
                                    id="spelling"
                                    placeholder="spelling"
                                    onChange={handleOnChange}
                                    onKeyDown={handleKeyDownSubmit}
                                />
                            </div>
                        </div>
                        <div className="my-wrapper sr-form-my-wrapper-more-info">
                            <div className="form-input-wrapper">
                                <div
                                    contentEditable="true"
                                    id="note"
                                    tabIndex="4"
                                    // onInput={(e) => {
                                    //     // e.preventDefault();
                                    //     e.stopPropagation();
                                    //     return false;
                                    // }}
                                    onBlur={handleHighlightAndGetWordInfo}
                                    onFocus={handleFocus}
                                    onKeyDown={handleKeydownHighlight}
                                    dangerouslySetInnerHTML={{
                                        __html: wordInfo.note,
                                    }}
                                ></div>
                            </div>

                            <div className="form-input-wrapper">
                                <div
                                    contentEditable="true"
                                    tabIndex="5"
                                    id="examples"
                                    // onInput={(e) => {
                                    //     e.preventDefault();
                                    //     // e.stopPropagation();
                                    // }}
                                    onBlur={handleHighlightAndGetWordInfo}
                                    onFocus={handleFocus}
                                    onKeyDown={handleKeydownHighlight}
                                    dangerouslySetInnerHTML={{
                                        __html: wordInfo.examples,
                                    }}
                                ></div>
                            </div>

                            <div className="form-input-wrapper sr-wrap-radio">
                                {levelArray &&
                                    levelArray.map((_level) => {
                                        return (
                                            <div
                                                key={`form-level-${_level}`}
                                                className="sr-radio-group"
                                            >
                                                <input
                                                    type="radio"
                                                    id={"level-" + _level}
                                                    className="sr-input-radio"
                                                    name="sr-radio-level"
                                                    value={_level}
                                                    checked={
                                                        _level == wordInfo.level
                                                    }
                                                />
                                                <label
                                                    className="sr-label-radio"
                                                    htmlFor={"level-" + _level}
                                                    onClick={handleChooseLevel}
                                                >
                                                    {_level}
                                                </label>
                                            </div>
                                        );
                                    })}
                            </div>
                        </div>
                    </div>
                    <div className="wrap-container wrapper-actions">
                        {/* <!-- <div className="form-input-wrapper">
        <button
            className="sr-btn sr-bg-secondary w-100"
            onclick="NodeFormDispatch({
        type: 'SHOW_FORM',
        payload: false
    })"
        >
            Cancel
        </button>
    </div> --> */}
                        <div className="form-input-wrapper">
                            <button
                                className="sr-btn sr-bg-primary w-100"
                                onClick={handleSubmit}
                            >
                                Submit
                            </button>
                        </div>
                        <div className="form-word base-actions-container">
                            <button
                                className="sr-btn action-btn btn-award"
                                onClick={handleShowProgressChart}
                            >
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    viewBox="0 0 576 512"
                                >
                                    <path d="M552 64H448V24c0-13.3-10.7-24-24-24H152c-13.3 0-24 10.7-24 24v40H24C10.7 64 0 74.7 0 88v56c0 35.7 22.5 72.4 61.9 100.7 31.5 22.7 69.8 37.1 110 41.7C203.3 338.5 240 360 240 360v72h-48c-35.3 0-64 20.7-64 56v12c0 6.6 5.4 12 12 12h296c6.6 0 12-5.4 12-12v-12c0-35.3-28.7-56-64-56h-48v-72s36.7-21.5 68.1-73.6c40.3-4.6 78.6-19 110-41.7 39.3-28.3 61.9-65 61.9-100.7V88c0-13.3-10.7-24-24-24zM99.3 192.8C74.9 175.2 64 155.6 64 144v-16h64.2c1 32.6 5.8 61.2 12.8 86.2-15.1-5.2-29.2-12.4-41.7-21.4zM512 144c0 16.1-17.7 36.1-35.3 48.8-12.5 9-26.7 16.2-41.8 21.4 7-25 11.8-53.6 12.8-86.2H512v16z"></path>
                                </svg>
                            </button>
                            <button
                                className="sr-btn action-btn btn-flash-cards"
                                onClick={handleShowVocaContainer}
                            >
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    viewBox="0 0 512 512"
                                >
                                    <path d="M284.3 11.7c-15.6-15.6-40.9-15.6-56.6 0l-216 216c-15.6 15.6-15.6 40.9 0 56.6l216 216c15.6 15.6 40.9 15.6 56.6 0l216-216c15.6-15.6 15.6-40.9 0-56.6l-216-216z"></path>
                                </svg>
                            </button>
                            <button
                                className="sr-btn action-btn btn-flash-cards"
                                onClick={handleShowNewWordList}
                            >
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    viewBox="30 -30 600 600"
                                >
                                    <g>
                                        <path d="m113.75 35h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                        <path d="m113.75 210h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                        <path d="m113.75 385h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                        <path d="m288.75 35h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                        <path d="m288.75 210h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                        <path d="m288.75 385h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                        <path d="m463.75 35h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                        <path d="m463.75 210h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                        <path d="m463.75 385h122.5c4.832 0 8.75 4.832 8.75 8.75v122.5c0 4.832-3.918 8.75-8.75 8.75h-122.5c-4.832 0-8.75-4.832-8.75-8.75v-122.5c0-4.832 3.918-8.75 8.75-8.75z" />
                                    </g>
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default FormCreate;
