import React, { useState, useEffect, useRef } from "react";
import chromeExtension from "../chromeExtension/chromeExtension";
import API_VOCA from "../firebase/API_VOCA";
import parseHtmlStringToWord from "../utils/parseHtmlStringToWord";

const TypingApp = ({ setShowComponent, data, setData }) => {
    const [words, setWords] = useState([]);
    const [wordActiveIndex, setWordActiveIndex] = useState(0);
    const [inputValue, setInputValue] = useState("");
    const [options, setOptions] = useState({
        amountWordShow: 50,
        pageNum: 1,
    });

    const currentWordElement = useRef(null);

    useEffect(() => {
        const fetchData = async () => {
            const data = await API_VOCA.get();
            setData(data);
            if (data.length > 0) {
                chromeExtension.sendMessage({
                    vocaAlarm: true,
                    alarmName: chromeExtension.reminder,
                    data: data[0],
                });
            }
        };

        if (data.length <= 0) {
            console.log("fetch data typing container");
            fetchData();
        }
        return () => {};
    }, []);

    useEffect(() => {
        if (data.length <= 0) return;
        const start = options.amountWordShow * (options.pageNum - 1);
        const end = options.amountWordShow * options.pageNum;
        if (end > data.length + options.amountWordShow) return;
        const wordsResult = data.slice(start, end).reduce((result, word) => {
            const words = [
                {
                    word: word.voca,
                    spelling: word.spelling,
                    typeWord: word.typeWords,
                    translate: word.translate,
                },
            ];
            // console.log("skldsjfklsdj");
            const wordsFromExample = parseHtmlStringToWord(word.examples) || [];
            const wordsFromNote = parseHtmlStringToWord(word.note) || [];
            // console.log({ wordsFromExample, wordsFromNote });
            const _result = [
                ...words,
                ...wordsFromExample,
                ...wordsFromNote,
            ].filter(Boolean);

            return [...result, ..._result];
        }, []);
        setWords([...words, ...wordsResult]);
    }, [options, data]);

    useEffect(() => {
        // 5 last word => get more word
        if (wordActiveIndex === words.length - 5) {
            setOptions((options) => ({
                ...options,
                pageNum: options.pageNum + 1,
            }));
        }
    }, [wordActiveIndex]);

    const handleInputKeyDown = (e) => {
        if (e.keyCode === 32) {
            const word = e.target.value;
            if (
                word.trim().toLowerCase() ===
                words[wordActiveIndex]?.word.trim().toLowerCase()
            ) {
                e.preventDefault();

                // reset box if match
                // e.target.value = "";
                setInputValue("");
                // handleScrollIntoView();
                setWordActiveIndex(wordActiveIndex + 1);
            }
        } else if (e.keyCode === 188) {
            setWordActiveIndex((prev) => prev - 1);
        } else if (e.keyCode === 190) {
            setWordActiveIndex((prev) => prev + 1);
        }
    };
    const handleInputChange = (e) => {
        const value = e.target.value;
        setInputValue(value);
    };

    const handleScrollIntoView = () => {
        const currentWordElement = document.querySelector(".sr-word.active");
        // console.log(currentWordElement);
        if (currentWordElement) {
            currentWordElement.scrollIntoView({
                behavior: "smooth",
                // block: "center",
            });
        }
    };

    return (
        <div className="sr-typing-app">
            <div className="sr-typing-app-container">
                <h2 className="sr-typing-app-title" onClick={setShowComponent}>
                    Typing App
                </h2>
                <div className="sr-typing-word-info-1">
                    ({words[wordActiveIndex]?.typeWord}) &#8226; /
                    {words[wordActiveIndex]?.spelling}/
                </div>
                <div className="sr-words-box">
                    {words.map((word, index) => {
                        const wordRef =
                            index == wordActiveIndex
                                ? currentWordElement
                                : null;
                        // console.log(wordRef);
                        return (
                            <Word
                                {...word}
                                ref={wordRef}
                                key={word.translate + word.word}
                                matched={index < wordActiveIndex}
                                active={index == wordActiveIndex}
                                inputValue={inputValue}
                            />
                        );
                    })}

                    <div
                        style={{
                            margin: "auto",
                            textAlign: "center",
                            fontSize: "16px",
                            color: "white",
                            height: "300px",
                            width: "100%",
                        }}
                    >
                        ----// Ending //----
                    </div>
                </div>
                <div className="sr-word-more-info">{`${words[wordActiveIndex]?.translate}`}</div>
                <input
                    className="sr-input-typing"
                    type="text"
                    value={inputValue}
                    onChange={handleInputChange}
                    onKeyDown={handleInputKeyDown}
                />
            </div>
        </div>
    );
};

const Word = ({
    word,
    matched,
    translate,
    typeWord,
    active,
    inputValue,
    spelling,
}) => {
    const [tooltipPosition, setTooltipPosition] = useState({ top: 0, left: 0 });
    const divRef = useRef(null);

    useEffect(() => {
        let timerId = null;
        if (active) {
            divRef.current.scrollIntoView({
                behavior: "smooth",
                block: "center",
            });

            // timerId = setTimeout(() => {
            //     // console.log("set position");
            //     const rect = divRef.current.getBoundingClientRect();

            //     setTooltipPosition({
            //         top: rect.top + "px",
            //         left: rect.left + rect.width / 2 + "px",
            //     });
            // }, 150);

            // console.log(divRef.current);
        }

        return () => {
            // if (timerId) {
            //     // console.log(timerId);
            //     clearInterval(timerId);
            // }
            // console.log("clear time interval");
        };
    }, [active]);

    return (
        <>
            <div
                ref={divRef}
                className={`sr-word ${matched ? "matched" : ""} ${
                    active ? "active" : ""
                }`}
            >
                {active
                    ? word.split("").map((letter, index) => {
                          if (index >= inputValue.length) {
                              return (
                                  <span
                                      className={
                                          index === inputValue.length
                                              ? "sr-caret-active"
                                              : ""
                                      }
                                  >
                                      {letter}
                                  </span>
                              );
                          } else {
                              return (
                                  <span
                                      className={
                                          inputValue[index]?.toLowerCase() ===
                                          letter?.toLowerCase()
                                              ? "sr-matched"
                                              : "sr-wrong"
                                      }
                                  >
                                      {letter}
                                  </span>
                              );
                          }
                      })
                    : word}
            </div>
            {/* {active && (spelling || typeWord) && (
                <div style={tooltipPosition} className="sr-typing-word-info">
                    ({typeWord}) &#8226; /{spelling}/
                </div>
            )} */}
        </>
    );
};

export default TypingApp;
