/* eslint-disable no-undef */
import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import CardItem from "./CardItem";
import API_VOCA from "../firebase/API_VOCA";
import chromeExtension from "../chromeExtension/chromeExtension";
import SearchField from "./SearchField";
import MissedContainer from "./MissedContainer";
import { appConfigLocalStorage } from "../utils/localStorage";
import { isNotTestMode } from "../utils/global";
import { extensionActions } from "../extension/constances";

// const initialData = [
//     {
//         voca: "this is vocab",
//         spelling: "lsdjfls",
//         meaning: "this is meaning of the vocab",
//         translate: "translate",
//         examples: "this is examples",
//         note: "note here",
//         typeWords: ["a", "b"],
//         time: [12345678234, 123456734],
//         similar: [],
//         opposite: [],
//         level: 1,
//         repetitiveTime: 1234567892,
//     },
//     {
//         voca: "this is vocab",
//         spelling: "lsdjfls",
//         meaning: "this is meaning of the vocab",
//         translate: "translate",
//         examples: "this is examples",
//         note: "note here",
//         typeWords: ["a", "b"],
//         time: [12345678234, 123456734],
//         similar: [],
//         opposite: [],
//         level: 1,
//         repetitiveTime: 1234567892,
//     },
// ];
const VocaContainer = ({
    handleShowComponent,
    data,
    setData,
    missedWords,
    setMissedWords,

    darkTheme,
    setDarkTheme,
}) => {
    // const [data, setData] = useState([]);
    // const [recallData, setRecallData] = useState(true);
    const [position, setPosition] = useState({
        besideContainer: false,
        side: "right",
    });
    const [options, setOptions] = useState({
        amountItemShow: 6,
        pageNum: 1,
    });

    const [multipleNextLevel, setMultipleNextLevel] = useState(false);

    const [totalWordsOverTime, setTotalWordsOverTime] = useState(null);

    const [config, setConfig] = useState({ nextLevel: { from: 0, to: 3 } });
    const [cardItemEvent, setCardItemEvent] = useState(false);
    // console.log(config.nextLevel);
    // console.log(config);
    useEffect(() => {
        const handleProgressData = async () => {
            if (totalWordsOverTime === null) {
                const currentTime = new Date().getTime();
                const _totalWordsOverTime = data.reduce((arr, el) => {
                    const remainingTime = el.repetitiveTime - currentTime;
                    return remainingTime <= 0 ? arr + 1 : arr;
                }, 0);

                setTotalWordsOverTime(_totalWordsOverTime);
            } else {
                setTotalWordsOverTime((prev) => prev - 1);
            }
            // const dataStorage = await API_VOCA.get(false);
            // console.log({ progressResult });
        };

        handleProgressData();
        return () => {};
    }, [data]);

    useEffect(() => {
        const fetchData = async () => {
            let data = [];
            chromeExtension.sendMessage(
                {
                    action: extensionActions.getVocabData,
                },
                function (response) {
                    // Xử lý phản hồi từ tiện ích mở rộng ở đây
                    // console.log("Dữ liệu từ tiện ích mở rộng:", response.data);
                    data = response.data;
                }
            );

            if (data.length <= 0) {
                data = await API_VOCA.get();
            }

            setData(data);
            // console.log(data);
            // if (data.length > 0) {
            //     chromeExtension.sendMessage({
            //         vocaAlarm: true,
            //         alarmName: chromeExtension.reminder,
            //         data: data[0],
            //     });
            // }
        };

        if (data.length <= 0 && isNotTestMode) {
            console.log("fetch data voca container");
            fetchData();
        }
        return () => {};
    }, []);

    useEffect(() => {
        if (multipleNextLevel) {
            console.log("sr-app: Multiple change");

            setTimeout(() => {
                // console.log("time out");
                setMultipleNextLevel(false);
            }, 3000);
        }

        return () => {};
    }, [multipleNextLevel]);

    const handleShowMore = () => {
        if (
            options.amountItemShow * (options.pageNum + 1) <=
            data.length + options.amountItemShow
        ) {
            console.log("COUNT PAGE");
            setOptions({
                ...options,
                pageNum: options.pageNum + 1,
            });
        }
    };

    const handleChangeTheme = () => {
        const oldConfig = appConfigLocalStorage.getAllItems();
        setDarkTheme((prev) => {
            const update = { ...oldConfig, theme: !prev };

            appConfigLocalStorage.setData(update);
            return !prev;
        });
    };

    const handleChangePosition = (position) => {
        setPosition((prev) => ({
            ...prev,
            besideContainer: !prev.besideContainer,
            side: position,
        }));
    };
    // console.log({ voca: darkTheme });
    return (
        <div
            className={`${darkTheme ? "sr-dark-mode" : ""} ${
                position.besideContainer
                    ? `sr-beside-wrapper sr-side-${position.side}`
                    : ""
            }`}
        >
            <div className="sr-overlay"></div>
            <div className={`card-container`}>
                <MissedContainer
                    data={missedWords}
                    setData={setMissedWords}
                    darkTheme={darkTheme}
                    setDarkTheme={setDarkTheme}
                    handleShowVocab={handleShowComponent}
                />
                <SearchField
                    data={data}
                    setData={setData}
                    setMissedWords={setMissedWords}
                />
                {multipleNextLevel && (
                    <div
                        style={{
                            color: "white",
                            width: "100%",
                            padding: "50px",
                            textAlign: "center",
                        }}
                    >
                        {" "}
                        Multiple Next Level Items...
                    </div>
                )}
                {data.length > 0 &&
                    data
                        .slice(
                            0,
                            multipleNextLevel
                                ? totalWordsOverTime
                                : options.amountItemShow * options.pageNum
                        )
                        .map((voca, index) => {
                            return (
                                <CardItem
                                    {...voca}
                                    key={voca.id + "vocaContainer"}
                                    indexInData={index}
                                    setData={setData}
                                    setMissedWords={setMissedWords}
                                    data={data}
                                    nextLevel={
                                        multipleNextLevel &&
                                        voca.level >= config.nextLevel.from &&
                                        voca.level <= config.nextLevel.to
                                            ? true
                                            : false
                                    }
                                    addEvent={cardItemEvent}
                                />
                            );
                        })}
            </div>
            <div
                style={{ maxWidth: "100vw" }}
                className="card-actions-container"
            >
                <div
                    className="sr-flex-center"
                    style={{
                        display: "inline-flex",
                        marginLeft: " 15px",
                        overflow: "hidden",
                        borderRadius: "999px",
                        border: "1px solid",
                        height: "fit-content",
                        margin: "auto",
                    }}
                >
                    <input
                        className="sr-get-words-input"
                        type="text"
                        // value={config.nextLevel.from}
                        placeholder={config.nextLevel.from}
                        onBlur={(e) => {
                            setConfig((prev) => {
                                return {
                                    ...prev,
                                    nextLevel: {
                                        ...prev.nextLevel,
                                        from: Number(e.target.value) || 0,
                                    },
                                };
                            });
                        }}
                    />
                    <input
                        style={{ borderLeft: "1px solid #ccc" }}
                        className="sr-get-words-input"
                        type="text"
                        // value={config.nextLevel.to}
                        placeholder={config.nextLevel.to}
                        onBlur={(e) => {
                            // console.log(e);
                            // console.log(e.target.value);
                            setConfig((prev) => {
                                // prev.nextLevel.to = Number(e.target.value) || 0;
                                return {
                                    ...prev,
                                    nextLevel: {
                                        ...prev.nextLevel,
                                        to: Number(e.target.value) || 3,
                                    },
                                };
                            });
                        }}
                    />
                </div>
                <button
                    style={{ color: "white" }}
                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn"
                    onClick={(e) => setMultipleNextLevel((prev) => !prev)}
                >
                    😱
                </button>
                <button
                    style={{ color: "white" }}
                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn"
                    onClick={(e) => setCardItemEvent((prev) => !prev)}
                >
                    {cardItemEvent ? "🙉" : "🙈"}
                </button>
                <button
                    style={{ color: "white" }}
                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn"
                    onClick={(e) => handleChangePosition("left")}
                >
                    &#8610;
                </button>
                <button
                    style={{ color: "white" }}
                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn"
                    onClick={(e) => handleChangePosition("right")}
                >
                    &#8611;
                </button>
                {options.amountItemShow * (options.pageNum + 1) <=
                    data.length + options.amountItemShow && (
                    <button
                        style={{ color: "white" }}
                        className="sr-btn btn-show-more sr-bg-yellow sr-circle-btn"
                        onClick={handleShowMore}
                    >
                        &#8480;
                    </button>
                )}

                <button
                    style={{
                        padding: "10px",
                        marginLeft: "15px",
                        color: "white",
                        // "--size": "38px",
                    }}
                    className="sr-btn sr-bg-primary sr-circle-btn"
                    onClick={handleChangeTheme}
                    dangerouslySetInnerHTML={{
                        __html: darkTheme ? "&#9728;" : "&#9770;",
                    }}
                ></button>
                <button
                    style={{
                        padding: "10px",
                        marginLeft: "15px",
                        color: "white",
                        // "--size": "38px",
                    }}
                    className="sr-btn sr-bg-primary sr-circle-btn"
                    onClick={handleChangeTheme}
                    dangerouslySetInnerHTML={{
                        __html: totalWordsOverTime,
                    }}
                ></button>

                <button
                    style={{ color: "white" }}
                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn"
                    onClick={handleShowComponent}
                >
                    &#10006;
                </button>
            </div>
        </div>
    );
};

VocaContainer.propTypes = {};

export default VocaContainer;
