import fetchWordInfo from "../dictionaryApi/dictionaryapi";

export const getWordInfo = async (word) => {
    const _wordInfo = await fetchWordInfo.get(word);
    if (!_wordInfo) {
        return {};
    }
    const spelling = _wordInfo.phonetic || "";
    const meaning = _wordInfo.meaning || "";
    const typeWord = _wordInfo.typeWord || "";
    return {
        ..._wordInfo,
        spelling,
        meaning,
        typeWords: typeWord,
    };
};

const textToHighlight = async (plainText) => {
    // const plainText = e.target.innerHTML;
    // let regex = /(<\/?\w+>|[^<>]+)/g;
    // const regex = /(<\/?\w+[^>]*>)|\s+([\w()\/]+)/g;
    const regex = /(<\/?\w+[^>]*>)|\s/g;
    // let textToArrayByTag = plainText.match(regex); // "this: that<div>word:</div>" => ['this: that', '<div>', 'word:', '</div>']
    const textToArrayByTag = plainText.split(regex).filter(Boolean);
    const wordArray = textToArrayByTag
        .map((string) => string.split(" "))
        .flat()
        .filter(Boolean); // ['this: that', '<div>', 'word:', '</div>'] => ['this:', 'that', '<div>', 'word:', '</div>']

    // console.log({ textToArrayByTag, wordArray });
    const resultPromise = wordArray.map(async (word) => {
        const _regex = /[:=\-+_]/; // check string includes special characters
        const firstCharacters = "=+-_";
        const getInfoCharacters = ":";

        // if first character is [=,-,+] then handle highlight;
        if (
            word.length > 1 &&
            (firstCharacters.includes(word[0]) ||
                getInfoCharacters.includes(word[word.length - 1]))
        ) {
            // console.log(`highlight word:`, word, "============");
            const className = getClassName(word);
            const wordOnly = word.replace(/[+=\-_:\/]/g, "").trim(); // remove special characters : = + ...
            let wordInfoHtmlText = "";
            if (word.includes(":")) {
                const { spelling, meaning, typeWords } = await getWordInfo(
                    wordOnly.replace("&nbsp;", "")
                );
                // console.log({ spelling, meaning, typeWords });
                wordInfoHtmlText = `${
                    typeWords &&
                    `<span class="sr-examples-typeWord">(${typeWords})</span>`
                } ${
                    spelling &&
                    `<span class="sr-examples-spelling sr-cl-yellow">/${spelling}/</span>`
                }`;
            }
            return `${
                className
                    ? `<span class="sr-examples-word ${className}">${wordOnly}</span>`
                    : wordOnly
            } ${wordInfoHtmlText}`;
        }
        // console.log("normal word", word);
        return word;
    });
    const resultArray = await Promise.all(resultPromise);
    const resultString = resultArray.join(" ");
    // setWordInfo((prev) => ({ ...prev, examples: resultString }));
    return resultString;

    function getClassName(word) {
        if (word.includes("=")) {
            return "sr-word-similar sr-cl-secondary";
        } else if (word.includes("+")) {
            return "sr-word-family sr-cl-primary";
        } else if (word.includes("-")) {
            return "sr-word-opposite sr-cl-danger";
        } else if (word.includes("_") || word.includes("~")) {
            return "sr-word-more sr-cl-green";
        }
        return "";
    }
};

export default textToHighlight;
