import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import googlePhotos from "../utils/googlePhotos";
import Spinner from "./Spinner";
import { appConfigLocalStorage } from "../utils/localStorage";

function Gallery({
    data,
    selected,
    imageClick,
    isShow: _isShow = true,
    searchValue = "",
    limit = 5,
}) {
    const [isShow, setIsShow] = useState(
        _isShow
            ? appConfigLocalStorage.getAllItems().showGallery || false
            : _isShow
    );
    const [images, setImages] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
    useEffect(() => {
        const timerId = setTimeout(() => {
            if (searchValue.trim() === "") {
                setImages([]);
                return;
            }

            const fetchImages = async () => {
                setIsLoading(true);
                // console.log({ searchValue: searchValue });
                const _photoUrl = await googlePhotos.searchImages(searchValue);
                setImages((prevImages) => {
                    // imageClick(_photoUrl[0] || {});
                    return _photoUrl.slice(0, limit);
                });
                setIsLoading(false);
            };
            // if component show
            if (isShow && images.length === 0) {
                fetchImages();
            } else if (isShow && _isShow) {
                fetchImages();
            }
        }, 800);
        return () => {
            clearTimeout(timerId);
        };
    }, [searchValue, isShow]);

    useEffect(() => {
        if (images.length > 0) {
            imageClick(images[0]);
        }
    }, [images]);

    const handleShowGallery = () => {
        setIsShow((prev) => {
            if (_isShow === true) {
                const oldConfig = appConfigLocalStorage.getAllItems();
                const update = { ...oldConfig, showGallery: !prev };

                appConfigLocalStorage.setData(update);
            }
            return !prev;
        });
    };

    return (
        <div>
            <div id="sr-album">
                {isShow && (
                    <ul class="stage">
                        {images &&
                            images?.map((image) => (
                                <li className="image-wrapper">
                                    <img
                                        className={`${
                                            selected === image.url
                                                ? "sr-selected"
                                                : ""
                                        }`}
                                        src={image.url}
                                        alt={image.alt}
                                        onClick={() =>
                                            imageClick(image, {
                                                showGallery: setIsShow,
                                            })
                                        }
                                    />
                                    {/* <span>Caption</span> */}
                                </li>
                            ))}
                    </ul>
                )}

                {isLoading && <Spinner size="30px" />}
                <div style={{ height: "16px" }} className="sr-gallery-actions">
                    <button
                        style={{ "--size": "14px" }}
                        className="sr-gallery-btn sr-circle-btn sr-bg-secondary"
                        onClick={handleShowGallery}
                    ></button>
                    <button
                        style={{ "--size": "14px" }}
                        className={
                            "sr-gallery-btn sr-circle-btn sr-bg-primary" +
                            `${selected === "" ? " no-image" : ""}`
                        }
                        onClick={() => imageClick({})}
                    ></button>
                </div>
            </div>
        </div>
    );
}

export default Gallery;
