import React, { useEffect, useRef, useState } from "react";
import { getDatamuse } from "../dictionaryApi/dictionaryapi";
// import CardItem from "./CardItem";
// import convertMilliseconds from "../utils/convertMilliseconds";

function PrintWindow({ data: _data, getWords, darkTheme }) {
    const [data, setData] = useState(_data);
    const [isShowWindow, setIsShowWindow] = useState(true);
    const [showRelatedNote, setShowRelatedNote] = useState(true);
    const [middle, setMiddle] = useState(data.length / 2);
    const [config, setConfig] = useState({
        showItemsBy: { level: { from: 0, to: 7 } },
        showRelateWords: false,
    });

    const actionsRef = useRef(null);
    // const [shuffleArray, setShuffleArray] = useState(0);

    let count = -getWords.to;

    const componentRef = useRef(null);
    let printTime = new Date().toJSON().slice(0, 10);

    // useEffect(() => {
    function shuffleArray() {
        const _data = [...data];
        _data.sort(() => Math.random() - 0.5);
        setData(_data);
    }
    //     shuffleArray();
    // }, [shuffleArray]);

    useEffect(() => {
        setData((prev) => {
            const newData = _data.filter((word) => {
                return (
                    word.level >= config.showItemsBy.level.from &&
                    word.level <= config.showItemsBy.level.to
                );
            });
            // console.log({ newData, prev });

            setMiddle(newData.length / 2);

            return newData;
        });
    }, [config.showItemsBy.level.to, config.showItemsBy.level.from]);

    useEffect(() => {
        if (componentRef.current) {
            componentRef.current.scrollIntoView({ behavior: "smooth" });
        }
    }, []);

    useEffect(() => {
        if (actionsRef.current) {
            actionsRef.current.scrollIntoView({ behavior: "smooth" });
        }
    }, [data]);

    const handleForcusExamplesAndNotes = async (e) => {
        const _text = await navigator.clipboard.readText();
        const text = _text.trim();
        // copyInputRef.current.value = text;
        await navigator.clipboard.writeText(text);
    };

    const handleHighlight = (e, from, to) => {
        // console.log({ from, to });
        const text = e.target.innerHTML;
        let highlight = text;
        data.forEach((word) => {
            highlight = highlight.onHighlightText(
                word.voca,
                // (text) => `[${word.translate}]`
                (text) => `${word.voca}`
            );
            // console.log({ highlight: highlight });
        });
        // highlight.replaceAll('<br>', "");
        e.target.innerHTML = highlight.replaceAll("<br>", "");
    };
    // console.log({ darkTheme });

    const handlePrint = () => {
        document.body.classList.add("sr-print-missed-words");
        window.print();
        window.addEventListener("afterprint", myFunction);

        function myFunction() {
            //   alert("This document is now being printed");
            document.body.classList.remove("sr-print-missed-words");
        }
    };
    return (
        <>
            {isShowWindow && (
                <div
                    className={
                        "sr-print-window" +
                        (showRelatedNote ? " sr-show-related-note" : "") +
                        (darkTheme ? " sr-dark-mode" : "")
                    }
                    ref={componentRef}
                >
                    <div className="sr-print-window-container">
                        <p
                            style={{
                                display: "flex",
                                alignItems: "center",
                                marginLeft: "15px",
                            }}
                            className="sr-print-window-title"
                        >
                            Today: {printTime}{" "}
                        </p>
                        <div className="sr-print-window-words-container">
                            {/* {data.map((word, index) => {
                                const isCorrect =
                                    (index + 1) % getWords.to === 0;
                                const isLastItem = index === data.length - 1;
                                if (isCorrect || isLastItem) {
                                    // console.log({
                                    //     count,
                                    //     result: count + getWords.to,
                                    // });
                                    count += getWords.to;
                                }
                                return (
                                    <>
                                        <WordTemplate
                                            key={word.id}
                                            {...word}
                                            setData={setData}
                                            indexInData={index}
                                        />
                                        {isCorrect || isLastItem ? (
                                            <div style={{ minWidth: "100%" }}>
                                                <GetQuestionChatGpt
                                                    data={data}
                                                    from={count}
                                                    to={index + 1}
                                                />
                                                <div
                                                    style={{ display: "flex" }}
                                                >
                                                    <div
                                                        onFocus={
                                                            handleForcusExamplesAndNotes
                                                        }
                                                        id="sr-passage"
                                                        className="passage-container sr-passage"
                                                        contentEditable="true"
                                                        onBlur={(e) =>
                                                            handleHighlight(
                                                                e,
                                                                count,
                                                                index + 1
                                                            )
                                                        }
                                                    ></div>
                                                    <ListWords
                                                        data={data}
                                                        from={count}
                                                        to={index + 1}
                                                    />
                                                </div>
                                                
                                            </div>
                                        ) : (
                                            <></>
                                        )}
                                    </>
                                );
                            })} */}

                            {/* {data.map((word, index) => {
                                return (
                                    <ListWords
                                        data={data}
                                        from={0}
                                        to={data.length / 2}
                                    />
                                );
                            })} */}

                            <div className="sr-print-list-word sr-show-detail-voca">
                                <ListWords2
                                    data={data}
                                    from={0}
                                    to={middle}
                                    setData={setData}
                                    showRelateWords={config.showRelateWords}
                                />

                                <ListWords2
                                    data={data}
                                    from={middle}
                                    to={data.length}
                                    setData={setData}
                                    showRelateWords={config.showRelateWords}
                                />
                            </div>
                            <Middle setMiddle={setMiddle} data={data} />
                            <div
                                ref={actionsRef}
                                className="sr-flex-center sr-no-print"
                            >
                                <div
                                    className="sr-flex-center sr-no-print"
                                    style={{
                                        display: "inline-flex",
                                        marginLeft: " 15px",
                                        overflow: "hidden",
                                        borderRadius: "999px",
                                        border: "1px solid",
                                    }}
                                >
                                    <input
                                        className="sr-get-words-input"
                                        type="text"
                                        placeholder={getWords.from}
                                        onBlur={(e) => {
                                            setConfig((prev) => {
                                                prev.showItemsBy.level.from =
                                                    Number(e.target.value) || 0;
                                                return { ...prev };
                                            });
                                        }}
                                    />
                                    <input
                                        style={{ borderLeft: "1px solid #ccc" }}
                                        className="sr-get-words-input"
                                        type="text"
                                        placeholder={7}
                                        onBlur={(e) => {
                                            setConfig((prev) => {
                                                prev.showItemsBy.level.to =
                                                    Number(e.target.value) || 0;
                                                return { ...prev };
                                            });
                                        }}
                                    />
                                </div>
                                <button
                                    style={{
                                        marginLeft: "15px",
                                    }}
                                    className="sr-btn btn-show-more sr-bg-primary sr-no-print sr-circle-btn"
                                    onClick={() =>
                                        setShowRelatedNote((prev) => !prev)
                                    }
                                >
                                    &#9869;
                                </button>
                                <button
                                    style={{ color: "white" }}
                                    className="sr-btn btn-show-more sr-bg-primary sr-circle-btn sr-no-print"
                                    onClick={handlePrint}
                                >
                                    &#8471;
                                </button>
                                <button
                                    style={{ color: "white" }}
                                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn sr-no-print"
                                    onClick={() =>
                                        setIsShowWindow((prev) => !prev)
                                    }
                                >
                                    &#10006;
                                </button>
                                <button
                                    style={{ color: "white" }}
                                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn sr-no-print"
                                    onClick={() => shuffleArray()}
                                >
                                    🔀
                                </button>
                                <button
                                    style={{ color: "white" }}
                                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn sr-no-print"
                                    onClick={() =>
                                        setConfig((prev) => ({
                                            ...prev,
                                            showRelateWords:
                                                !prev.showRelateWords,
                                        }))
                                    }
                                >
                                    🖖
                                </button>
                                <button
                                    style={{ color: "white" }}
                                    className="sr-btn btn-show-more sr-bg-secondary sr-circle-btn sr-no-print"
                                >
                                    {data.length}
                                </button>
                            </div>
                            <div style={{ display: "flex", minWidth: "100%" }}>
                                <div>
                                    <GetQuestionChatGpt
                                        data={data}
                                        from={0}
                                        to={middle}
                                    />
                                    <div
                                        style={{
                                            padding: "5px",
                                        }}
                                        onFocus={handleForcusExamplesAndNotes}
                                        id="sr-passage"
                                        className="passage-container sr-passage"
                                        contentEditable="true"
                                        onBlur={(e) =>
                                            handleHighlight(e, 0, data.length)
                                        }
                                    ></div>
                                </div>
                                <div>
                                    <GetQuestionChatGpt
                                        data={data}
                                        from={middle}
                                        to={data.length + 1}
                                    />
                                    <div
                                        style={{
                                            padding: "5px",
                                            marginLeft: "5px",
                                        }}
                                        onFocus={handleForcusExamplesAndNotes}
                                        id="sr-passage"
                                        className="passage-container sr-passage"
                                        contentEditable="true"
                                        onBlur={(e) =>
                                            handleHighlight(e, 0, data.length)
                                        }
                                    ></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
}

function Middle({ setMiddle, data }) {
    return (
        <div
            className="sr-flex-center sr-no-print"
            style={{
                display: "inline-flex",
                marginLeft: " 15px",
                overflow: "hidden",
                borderRadius: "999px",
                border: "1px solid",
            }}
        >
            <input
                className="sr-get-words-input"
                type="text"
                placeholder={data.length / 2}
                onBlur={(e) => setMiddle(Number(e.target.value))}
            />
        </div>
    );
}

function ListWords2({ data = [], from, to, setData, showRelateWords }) {
    return (
        <div id="list-words sr-no-print">
            <ul>
                {data?.slice(from, to)?.map((word, index) => (
                    <SimpleWordTemplate
                        key={word.id + "list"}
                        {...word}
                        showRelateWords={showRelateWords}
                        setData={setData}
                        from={from}
                        to={to}
                        index={index}
                    />
                ))}
            </ul>
        </div>
    );
}

const SimpleWordTemplate = (props) => {
    const [info, setInfo] = useState(props);
    useEffect(() => {
        // console.log(indexInData, searchText, !props.nextLevel);
        if (props.showRelateWords) {
            async function getRelatedWords() {
                // console.log()
                const [_spellLike, _meaningLike] = await Promise.all([
                    getDatamuse(info.voca, "sp"),
                    getDatamuse(info.voca, "ml"),
                ]);

                const spellLike =
                    _spellLike?.length > 0
                        ? `<div>Sound : ${_spellLike
                              ?.slice(1, 6)
                              .map((word) => word.word + " ")
                              .join(" / ")}</div>`
                        : "";
                const similarMeaning =
                    _meaningLike?.length > 0
                        ? `<div>Meaning : ${_meaningLike
                              ?.slice(0, 5)
                              .map((word) => word.word + " ")
                              .join(" / ")}</div>`
                        : "";
                setInfo((prev) => {
                    return {
                        ...prev,
                        note: `${spellLike} ${similarMeaning} ${prev.note}`,
                    };
                });
                // console.log({
                //     similarMeaningWords: spellLike,
                //     similarSoundWords: meaninglike,
                // });
            }

            getRelatedWords();
        }
    }, [props.showRelateWords]);

    const handleDelete = (indexInData) => {
        // Update Ui
        props.setData((prevData) => {
            const newData = [...prevData];
            newData.splice(indexInData, 1);
            return newData;
        });
    };

    return (
        <li>
            <p
                // style={{ fontSize: "20px" }}
                className="sr-word-title sr-max-line-1"
            >
                {info.voca} ({" "}
                {typeof info.typeWords !== "string" &&
                    info.typeWords.map((type) => type + " ")}
                ) /<span className="sr-cl-yellow">{info.spelling}</span>/{" "}
                <button
                    className="sr-print-word-delete sr-no-print"
                    onClick={() =>
                        handleDelete(Math.floor(props.from + props.index))
                    }
                >
                    X
                </button>{" "}
                {props.level} _ {Math.floor(props.from + props.index + 1)}
            </p>
            {/* <p className="sr-max-line-2">{info.translate}</p> */}
            <p className="sr-max-line-2">
                [<span className="sr-cl-primary">{info.translate}</span>]
            </p>

            <div className="padding-left list-words-more-info">
                {info.note && (
                    <div
                        className="sr-word-addition sr-card-note-container"
                        dangerouslySetInnerHTML={{
                            __html: info.note,
                        }}
                    ></div>
                )}
                {info.examples && (
                    <div
                        className="sr-word-addition sr-card-note-container"
                        dangerouslySetInnerHTML={{
                            __html: info.examples,
                        }}
                    ></div>
                )}
            </div>
        </li>
    );
};

function WordTemplate({
    voca,
    translate,
    meaning,
    imageUrl,
    examples,
    similar,
    note,
    spacedRepetition,
    time,
    spelling,
    typeWords,
    repetitiveTime,
    level,
    id,
    indexInData,
    setData,
    setMissedWords,
}) {
    // if (voca.toUpperCase() === "GRAMMAR") return false;
    // const currentTime = new Date().getTime();
    // const remainingTime = repetitiveTime - currentTime;

    const tagClassNames = [
        "sr-similar-spelling-and-meaning",
        "sr-similar-spelling",
        "sr-similar-meaning sr-bg-danger",
        "sr-word-synonym",
    ];

    const handleDelete = () => {
        // Update Ui
        setData((prevData) => {
            const newData = [...prevData];
            newData.splice(indexInData, 1);
            return newData;
        });
    };
    return (
        <div
            className={`sr-card sr-show-detail-voca`}
            // style="order: -57142365"
            // style={{ order: remainingTime }}
            data-level={indexInData}
        >
            <div className="sr-card-header">
                <h2 id="voca" className="sr-title" contentEditable="true">
                    {voca}{" "}
                    <button
                        className="sr-print-word-delete"
                        onClick={handleDelete}
                    >
                        X
                    </button>
                </h2>
                {typeWords && typeWords.length > 0 && (
                    <div className="sr-type-word-container">
                        {typeof typeWords !== "string" &&
                            typeWords.map((typeWord) => (
                                <span className="type-words">
                                    (<span id="typeWords">{typeWord}</span>)
                                </span>
                            ))}
                    </div>
                )}

                {spelling && (
                    <span className="spelling">
                        /
                        <span id="spelling" contentEditable="true">
                            {spelling}
                        </span>
                        /
                    </span>
                )}
            </div>

            <div className="sr-tag-words-container">
                {similar.map((word, index) => (
                    <button
                        className={
                            "sr-btn sr-tag-word " +
                            (tagClassNames[word._typeSimilar] || "")
                        }
                        data-info={`${word.typeWords} /${
                            word.spelling || ""
                        }/ ${word.translate}`}
                    >
                        {word.voca}
                    </button>
                ))}
            </div>

            <div>
                <div>
                    <span
                        id="meaning"
                        className="note meaning"
                        contentEditable="true"
                    >
                        {meaning}
                    </span>
                    {translate && (
                        <>
                            <span className="sr-meaning-flag">|</span>
                            <p
                                id="translate"
                                className="note sr-translate"
                                contentEditable="true"
                            >
                                {translate}
                            </p>
                        </>
                    )}
                </div>
            </div>

            {note.trim() && (
                <div
                    id="note"
                    className="sr-note"
                    contentEditable="true"
                    dangerouslySetInnerHTML={{
                        __html: note.onHighlightText(voca),
                    }}
                ></div>
            )}
            {examples.trim() && (
                <div
                    id="examples"
                    className="example-container sr-example sr-no-print"
                    contentEditable="true"
                    dangerouslySetInnerHTML={{
                        __html: examples.onHighlightText(voca),
                    }}
                ></div>
            )}
        </div>
    );
}

function ListWords({ data = [], from, to }) {
    return (
        <div id="list-words sr-no-print">
            <ul>
                {data?.slice(from, to)?.map((word) => (
                    <li>
                        <p className="sr-max-line-1">
                            {word.voca} ({" "}
                            {typeof word.typeWords !== "string" &&
                                word.typeWords.map((type) => type + " ")}
                            ) /{word.translate}/
                        </p>
                        <p className="sr-max-line-1 sr-cl-primary">
                            [{word.translate}]
                        </p>
                    </li>
                ))}
            </ul>
        </div>
    );
}

const GetQuestionChatGpt = ({ data = [], from, to }) => {
    const [getWords, setGetWords] = useState({ from, to });

    const handleGetQuestion = async (e) => {
        e.stopPropagation();
        if (data.length > 0) {
            const wordsString = data
                .slice(getWords.from, getWords.to)
                .map((word) => word.voca)
                .join(", ");
            const question = `
                    Đặt câu cho những từ sau bằng tiếng việt yêu cầu liệt kê theo danh sách sau đó dịch các câu đó bằng tiếng anh ở phía bên dưới: ${wordsString}

                                
                    Vd cho từ Hello:
                    1. Urge (V, n): 'Tôi khuyên bạn nên đi khám bác sĩ.'
                    <Khuyên nhủ, thúc giục> /ɜ:dʒ/
                    Vd cho từ Entrepreneur:
                    2. Particular (A, n): "Tôi muốn biết thông tin cụ thể về sản phẩm này."
                    <Cụ thể, đặc biệt> /pər'tɪkjələr/

                    Dịch câu tiếng việt sang tiếng anh:
                    1. Urge (V, n) /ɜ:dʒ/: "I urge you to go see a doctor."
                    2. Particular (A, n) /pər'tɪkjələr/: "I want to know the particular information about this product." 
               `;
            // const question = `
            // Đặt câu cho những từ sau bằng tiếng việt yêu cầu liệt kê theo danh sách sau đó dịch các câu đó bằng tiếng anh ở phía bên dưới: ${wordsString}

            // Vd cho từ Hello:
            // 1. Urge (V, n): 'Tôi khuyên bạn nên đi khám bác sĩ.'
            //    <Khuyên nhủ, thúc giục> /ɜ:dʒ/: "I urge you to go see a doctor."
            // Vd cho từ Entrepreneur:
            // 2. Particular (A, n): "Tôi muốn biết thông tin cụ thể về sản phẩm này."
            //    <Cụ thể, đặc biệt> /pər'tɪkjələr/: "I want to know the particular information about this product."
            //    `;

            await navigator.clipboard.writeText(question);
        }
    };

    return (
        <div
            className="sr-flex-center sr-no-print"
            style={{ display: "inline-flex" }}
        >
            <button
                style={{
                    padding: "10px",
                    marginLeft: "15px",
                    background: "#1ac27d",
                }}
                className="sr-btn"
                onClick={handleGetQuestion}
            >
                ChatGpt
            </button>
            <div
                className="sr-flex-center"
                style={{
                    display: "inline-flex",
                    marginLeft: " 15px",
                    overflow: "hidden",
                    borderRadius: "999px",
                    border: "1px solid",
                }}
            >
                <input
                    className="sr-get-words-input"
                    type="text"
                    placeholder={getWords.from}
                    onBlur={(e) =>
                        setGetWords((prev) => ({
                            ...prev,
                            from: Number(e.target.value) || from || 0,
                        }))
                    }
                />
                <input
                    style={{ borderLeft: "1px solid #ccc" }}
                    className="sr-get-words-input"
                    type="text"
                    placeholder={getWords.to}
                    onBlur={(e) =>
                        setGetWords((prev) => ({
                            ...prev,
                            to: Number(e.target.value) || to || 20,
                        }))
                    }
                />
            </div>
        </div>
    );
};

export default PrintWindow;
