const parseHtmlStringToWord = (htmlString) => {
    if (!htmlString.trim()) return;
    const parser = new DOMParser();
    const doc = parser.parseFromString(htmlString, "text/html");

    // const divElement = doc.querySelector("#examples");
    const spanElements = doc.querySelectorAll("span.sr-examples-word");

    const result = Array.from(spanElements).map((spanElement) => {
        const word = spanElement.textContent.trim();
        const typeWord =
            spanElement?.nextElementSibling?.textContent.trim() || [];
        const spelling =
            spanElement.nextElementSibling?.nextElementSibling?.textContent.trim() ||
            [];
        const translate = spanElement?.parentElement?.textContent
            .replace(word, "")
            .replace(typeWord, "")
            .replace(spelling, "")
            .trim();

        return { word, typeWord, spelling, translate };
    });

    return result;
};

export default parseHtmlStringToWord;
